import React, { Component } from "react";
import { branch } from "../components/CustomComponent.js";
export default class Loading extends Component {
  componentDidMount() {
    if (localStorage.getItem("ez-acc-tk")) {
      if(branch){
        window.location.href = "/admin/manage/branch";
      }else{
        window.location.href = "/console";
      }
    }else{
      window.location.href = "/login";
    }
  }
  render() {
    return <div></div>;
  }
}
