       {this.props.type === "approve" && (<div className="status bg-purple-light wpx-25 hpx-25 icon px-1 pointer text-purple" title="เปลี่ยนแปลงสถานะ" onClick={() => {this.props.onClick()}}>{"\uf00c"}</div>)}
       {this.props.type === "delete" && (<div className="status bg-danger-light wpx-25 hpx-25 icon px-1 pointer text-danger" title="ลบข้อมูล" onClick={() => {this.props.onClick()}}>{"\uf1f8"}</div>)} 
