import React, { Component } from "react";
import { POST, loading, branch } from "../components/CustomComponent.js";

import "../assets/css/bootstrap.css";
import "../assets/css/style.css";
export default class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: "",
      password_view: "password",
      remember: false
    };
  }
  componentDidMount() {
    loading(false);
    if (localStorage.getItem("ez-acc-tk")) {
      if(branch){
        window.location.href = "/admin/manage/branch";
      }else{
        window.location.href = "/console";
      }
    }
  }
  Login = async () => {
    loading(true);
    let body = {
      email: this.state.email,
      password: this.state.password,
      remember: this.state.remember,
    };
    let result = await POST("login", body);
    if (result?.status) { 
      localStorage.setItem("ez-acc-tk", btoa("Token " + result.data.token));
      localStorage.setItem("ez-profile", JSON.stringify({...result.data, password: ""}));
      window.location.href = "/console";
    }
    loading(false);
  };
  render() {
    return (
      <div className="w-100 min-vh-100 background d-flex justify-content-center align-items-center">
          <div className="col-10 col-sm-8 col-md-5 col-lg-4 col-xl-4 col-xxl-3 mx-auto">
            <div className="card shadow-lg border-0 p-4">
                  <h3 className="text-center mb-3">เข้าสู่ระบบ</h3>
                  <label className="mb-1">อีเมล</label>
                  <input
                    className="form-control mb-3"
                    type="text"
                    placeholder="อีเมล"
                    onChange={(e) => {
                      this.setState({ email: e.target.value });
                    }}
                    value={this.state.email}
                  />
                  <label className="mb-1">รหัสผ่าน</label>
                  <div className="position-relative">
                  <input
                    className="form-control mb-3"
                    type={this.state.password_view}
                    placeholder="รหัสผ่าน"
                    onChange={(e) => {
                      this.setState({ password: e.target.value });
                    }}
                    onKeyUp={(e) => {
                      if (e.key === "Enter") {
                        this.Login();
                      }
                    }}
                    value={this.state.password}
                  />
                  <div className="wpx-40 hpx-30 d-flex justify-content-center align-items-center icon pointer position-absolute" style={{right:0, top:1}} onClick={() => {this.setState({password_view: this.state.password_view === "password" ? "text" : "password"})}}>{this.state.password_view === "password" ? "\uf06e" : "\uf070"}</div>
                  </div>
                  <div className="d-flex mb-3">
                    <input type="checkbox" className="me-2" onChange={(e)=>{this.setState({remember:e.target.checked})}} checked={this.state.remember}/>
                    <span>จดจำการเข้าสู่ระบบ</span>
                  </div>
                  <button
                    type="button"
                    className="btn btn-success w-100"
                    onClick={() => {
                      this.Login();
                    }}
                  >
                    เข้าสู่ระบบ
                  </button>
            </div>
            <div className="w-100 text-center mt-2">Copyright © 2023 EzClinic version {process.env.REACT_APP_VERSION}</div>
          </div>
      </div>
    );
  }
}
