import React, { Component } from "react";
import { profile, branch, alert, total, float, toFixed, format_date, random_charactor, GET, POST, PUT, DELETE, loading, Status, params, DatePicker, Select, BtnIcon, Swal, Pagination, Switch, Navbar, Modal, Resizer } from "../../../components/CustomComponent.js";
export default class Customer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modal_create: false,
      modal_update: false,
      modal_view: false,

      search: "",

      data: [],
      count: 0,

      position_id: "",
      position_name: "",
      user_type: "",
      employment_type: "",
    };
  }

  async componentDidMount() {
    loading(false);
    await this.Get(1);
  }
  Get = async (page) => {
    loading(true);
    let result = await GET("admin/manage/customer/list", { page: page, data_search: this.state.search });
    if (result?.status) {
      this.setState({ data: result.data, count: result.count });
    }
    loading(false);
  };
  CreateUpdate = async () => {
    if (!this.state.position_name.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณากรอกชื่อตำแหน่ง");
      return;
    } else if (!this.state.user_type.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณาเลือกประเภทพนักงาน");
      return;
    } else if (!this.state.employment_type.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณาเลือกประเภทการจ้างงาน");
      return;
    }

    loading(true);
    let body = {
      position_name: this.state.position_name,
      user_type: this.state.user_type,
      employment_type: this.state.employment_type,
    };
    let result = !this.state.position_id ? await POST("admin/manage/position/create", body) : await PUT("admin/manage/position/update/" + this.state.position_id, body);
    if (result?.status) {
      alert("#", "success");
    }
    loading(false);
  };
  Delete = async (id) => {
    if (await alert("", "warning", "ลบข้อมูล", "ยืนยันการลบรายการนี้หรือไม่ ?", "ลบข้อมูล", "ยกเลิก")) {
      loading(true);
      let result = await DELETE("admin/manage/customer/delete/" + id, null);
      if (result?.status) {
        alert("#", "success");
      }
      loading(false);
    }
  };
  Clear = () => {
    this.setState({
      position_id: "",
      position_name: "",
      user_type: "",
      employment_type: "",
    });
  };
  render() {
    return (
      <div className="w-100 min-vh-100">
        <Navbar />
        <div className="p-4">
          <h3 className="mb-3">ลูกค้า</h3>
          <div className="d-flex flex-wrap">
            <input
              type="text"
              className="form-control wpx-250 me-2 mb-2"
              placeholder="ค้นหา..."
              onChange={(e) => {
                this.setState({ search: e.target.value });
              }}
              onKeyUp={(e) => {
                if (e.key === "Enter") {
                  this.Get(1);
                }
              }}
              value={this.state.search}
            />
            <div className="d-flex">
              <button
                className="btn btn-success px-4 me-2 mb-2"
                onClick={() => {
                  this.Get(1);
                }}
              >
                ค้นหา
              </button>
              <button
                className="btn btn-outline-success px-4 me-2 mb-2"
                onClick={() => {
                  window.location.reload();
                }}
              >
                ล้างค่า
              </button>
            </div>
            <button
              className="btn btn-success px-3 ms-auto mb-2"
              onClick={async () => {
                window.location.href = "/admin/customer/create-customer";
              }}
            >
              + เพิ่มข้อมูล
            </button>
          </div>
          <div className="table-responsive">
            <table className="table table-borderless rounded-3 overflow-hidden table-hover">
              <thead>
                <tr>
                  <td>ลูกค้า</td>
                  <td>เพศ/วันเกิด</td>
                  <td>อาชีพ/รายได้</td>
                  <td>ที่อยู่</td>
                  <td>ยอดการสั่งซื้อ/สั่งซื้อล่าสุด</td>
                  <td className="wpx-110"></td>
                </tr>
              </thead>
              <tbody>
                {this.state.data?.map((item, index) => (
                  <tr>
                    <td>
                      <div className="d-flex">
                        <div className="me-3 text-center">
                          <img className="rounded-circle" alt="example" src={item.picture ? item.picture : require(`../../../assets/images/profile/${(index % 30) + 1}.png`)} style={{ width: 25, height: 25, objectFit: "cover" }} />
                          <div className="bg-success text-white px-3 rounded-pill mt-2">ลูกค้าทั่วไป</div>
                        </div>
                        <div>
                          <div>
                            <div class="d-flex">
                              {item.customer_code} <div className="bg-secondary-light px-2 rounded-pill ms-1">ความงาม 2</div>
                            </div>
                            <span>
                              {item.firstname} {item.lastname} ({item.nickname})
                            </span>{" "}
                            <br />
                            <div>
                              <span className="icon text-success me-2">{"\uf095"}</span>
                              {item.phone || "-"}
                            </div>
                          </div>
                        </div>
                      </div>
                    </td>
                    <td>
                      <span>{item.gender || "-"}</span> <br />
                      <span>{format_date(item.birthdate)}</span>
                    </td>
                    <td>
                      <span>{item.career || "-"}</span> <br />
                      <span>{toFixed(item.revenue)}</span>
                    </td>
                    <td>{item.source || "-"}</td>
                    <td></td>
                    <td className="text-center align-middle">
                      <div className="d-flex">
                        <BtnIcon
                          type="view"
                          onClick={() => {
                            window.location.href = "/admin/customer/view-customer?id=" + item?.customer_id;
                          }}
                        />
                        <BtnIcon
                          type="update"
                          onClick={() => {
                            window.location.href = "/admin/customer/update-customer?id=" + item?.customer_id;
                          }}
                        />
                        <BtnIcon
                          type="delete"
                          onClick={() => {
                            this.Delete(item?.customer_id);
                          }}
                        />
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
          <div className="w-100 d-flex align-items-center justify-content-between flex-wrap mt-2">
            <span className="mb-3">รายการทั้งหมด {this.state.count || 0} รายการ</span>
            <Pagination className="mb-3" count={Math.ceil(this.state.count / 10) || 1} showFirstButton showLastButton onChange={(_, page) => this.Get(page)} />
          </div>
        </div>
      </div>
    );
  }
}
