import React, { Component } from "react";
import { profile, branch, alert, total, float, toFixed, format_date, random_charactor, GET, POST, PUT, DELETE, loading, Status, params, DatePicker, Select, BtnIcon, Swal, Pagination, Switch, Navbar, Modal, Resizer } from "../../../components/CustomComponent.js";
const prefix_name = ["นาย", "นาง", "นางสาว", "นายแพทย์", "แพทย์หญิง", "หมอ"].map((e) => {
  return { value: e, label: e };
});
export default class Partner extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modal_create: false,
      modal_update: false,

      search: "",

      data: [],
      count: 0,

      prefix: "",
      fullname: "",
      tax_number: "",
      phone: "",
      address: "",
    };
  }

  async componentDidMount() {
    loading(false);
    await this.Get(1);
  }
  Get = async (page) => {
    loading(true);
    let result = await GET("admin/manage/partner/list", { page: page, data_search: this.state.search });
    if (result?.status) {
      this.setState({ data: result.data, count: result.count });
    }
    loading(false);
  };
  CreateUpdate = async () => {
    if (!this.state.prefix.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณาเลือกคำนำหน้า");
      return;
    } else if (!this.state.fullname.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณากรอกชื่อ - นามสกุล");
      return;
    } else if (!this.state.tax_number.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณากรอกเลขที่ผู้เสียภาษี");
      return;
    } else if (!this.state.phone.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณากรอกเบอร์โทรศัพท์");
      return;
    }

    loading(true);
    let body = { prefix: this.state.prefix,
      fullname: this.state.fullname,
      tax_number: this.state.tax_number,
      phone: this.state.phone,
      address: this.state.address, };
    let result = !this.state.partner_id ? await POST("admin/manage/partner/create", body) : await PUT("admin/manage/partner/update/" + this.state.partner_id, body);
    if (result?.status) {
      alert("#", "success");
    }
    loading(false);
  };
  Delete = async (id) => {
    if (await alert("", "warning", "ลบข้อมูล", "ยืนยันการลบรายการนี้หรือไม่ ?", "ลบข้อมูล", "ยกเลิก")) {
      loading(true);
      let result = await DELETE("admin/manage/partner/delete/" + id, null);
      if (result?.status) {
        alert("#", "success");
      }
      loading(false);
    }
  };
  Clear = () => {
    this.setState({
      partner_id: "",
      partner_name: "",
      user_type: "",
      employment_type: "",
    });
  };
  render() {
    const { modal_create, modal_update, search, data, count, prefix, fullname, tax_number, phone, address } = this.state;
    return (
      <div className="w-100 min-vh-100">
        <Navbar />
        <div className="p-4">
          <h3 className="mb-3">คู่ค้า</h3>
          <div className="d-flex flex-wrap">
            <input
              type="text"
              className="form-control wpx-250 me-2 mb-2"
              placeholder="ค้นหา..."
              onChange={(e) => {
                this.setState({ search: e.target.value });
              }}
              onKeyUp={(e) => {
                if (e.key === "Enter") {
                  this.Get(1);
                }
              }}
              value={this.state.search}
            />
            <div className="d-flex">
              <button
                className="btn btn-success px-4 me-2 mb-2"
                onClick={() => {
                  this.Get(1);
                }}
              >
                ค้นหา
              </button>
              <button
                className="btn btn-outline-success px-4 me-2 mb-2"
                onClick={() => {
                  window.location.reload();
                }}
              >
                ล้างค่า
              </button>
            </div>
            <button
              className="btn btn-success px-3 ms-auto mb-2"
              onClick={async () => {
                this.Clear();
                this.setState({ modal_create: true,prefix: "",
                  fullname: "",
                  tax_number: "",
                  phone: "",
                  address: "", });
              }}
            >
              + เพิ่มข้อมูล
            </button>
          </div>
          <div className="table-responsive">
            <table className="table table-borderless rounded-3 overflow-hidden table-hover">
              <thead>
                <tr>
                  <td>ชื่อผู้ค้า</td>
                  <td>เลขผู้เสียภาษี</td>
                  <td>เบอร์โทรศัพท์</td>
                  <td className="wpx-80"></td>
                </tr>
              </thead>
              <tbody>
                {this.state.data?.map((item, index) => (
                  <tr>
                    <td>{item.fullname}</td>
                    <td>{item.tax_number}</td>
                    <td>{item.phone}</td>
                    <td>
                      <div className="d-flex">
                        <BtnIcon
                          type="update"
                          onClick={() => {
                            this.setState({
                              ...item,
                              modal_update: true,
                            });
                          }}
                        />
                        <BtnIcon
                          type="delete"
                          onClick={() => {
                            this.Delete(item?.partner_id);
                          }}
                        />
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
          <div className="w-100 d-flex align-items-center justify-content-between flex-wrap mt-2">
            <span className="mb-3">รายการทั้งหมด {this.state.count || 0} รายการ</span>
            <Pagination className="mb-3" count={Math.ceil(this.state.count / 10) || 1} showFirstButton showLastButton onChange={(_, page) => this.Get(page)} />
          </div>
        </div>
        {/* CREATE UPDATE */}
        <Modal show={this.state.modal_create || this.state.modal_update} onHide={() => this.setState({ modal_create: false, modal_update: false })}>
          <Modal.Header closeButton>
            <h5 className="mb-0">{this.state.modal_create ? "เพิ่มคู่ค้า" : this.state.modal_update ? "แก้ไขคู่ค้า" : ""}</h5>
          </Modal.Header>
          <Modal.Body>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">คำนำหน้า</div>
               <Select
                                  className="form-control"
                                  options={prefix_name}
                                  onChange={(e) => {
                                    this.setState({ prefix: e });
                                  }}
                                  value={this.state.prefix}
                                />
            </div>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">ชื่อ - นามสกุล</div>
              <input
                type="text"
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  this.setState({ fullname: e.target.value });
                }}
                value={this.state.fullname}
              />
            </div>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">เลขที่ผู้เสียภาษี</div>
              <input
                type="text"
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  this.setState({ tax_number: e.target.value });
                }}
                value={this.state.tax_number}
              />
            </div>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">เบอร์โทรศัพท์</div>
              <input
                type="text"
                className="form-control"
                placeholder="กรอกข้อมูล..."
                maxLength={10}
                onChange={(e) => {
                  if(!float(e.target.value)){return;}
                  this.setState({ phone: e.target.value });
                }}
                value={this.state.phone}
              />
            </div>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">ที่อยู่</div>
              <textarea
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  this.setState({ address: e.target.value });
                }}
                value={this.state.address}
              />
            </div>
          </Modal.Body>
          <Modal.Footer className="py-1 d-flex justify-content-end">
            <button
              className="btn btn-success px-5 me-1"
              onClick={() => {
                this.CreateUpdate();
              }}
            >
              บันทึก
            </button>
            <button
              className="btn btn-secondary px-5 ms-1"
              onClick={() => {
                this.setState({ modal_create: false, modal_update: false });
              }}
            >
              ยกเลิก
            </button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}
