  calculateWeeklyHours(id) {
    let schedule = this.state.data_employee?.find((e) => e.user_id === id) || null;
    let totalMinutes = 0;
    if(schedule){
    for (const [key, value] of Object.entries(schedule)) {
      if (key.startsWith("start_time_")) {
        const day = key.split("_")[2];
        const startTime = schedule[`start_time_${day}`]?.replaceAll(".", ":");
        const endTime = schedule[`end_time_${day}`]?.replaceAll(".", ":");

        const [s_hours, s_minutes] = startTime?.split(':').map(Number) || [0, 0];
        let start_minutes = s_hours * 60 + s_minutes
        const [e_hours, e_minutes] = endTime?.split(':').map(Number) || [0, 0];
        let end_minutes = e_hours * 60 + e_minutes
        totalMinutes += end_minutes - start_minutes;
      }
    }
  
    const totalHours = Math.floor(totalMinutes / 60);
    const remainingMinutes = totalMinutes % 60;
  
    return `${totalHours} ชั่วโมง ${remainingMinutes} นาที`;
  } else{
    return `0 ชั่วโมง`;
  }
  }
