import React, { Component } from "react";
import { profile, branch, alert, total, float, toFixed, format_date, random_charactor, GET, POST, PUT, DELETE, loading, Status, params, DatePicker, Select, BtnIcon, Swal, Pagination, Switch, Navbar, Modal, Resizer }from "../../../components/CustomComponent.js";
export default class SaleChannel extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modal_create: false,
      modal_update: false,
      modal_view: false,

      search: "",

      data: [],
      count: 0,

      position_id: "",
      position_name: "",
      user_type: "",
      employment_type: "",
    };
  }

  async componentDidMount() {
    loading(false);
    await this.Get(1);
  }
  Get = async (page) => {
    loading(true);
    let result = await GET("admin/manage/position/list", { page: page, data_search: this.state.search });
    if (result?.status) {
      this.setState({ data: result.data, count: result.count });
    }
    loading(false);
  };
  CreateUpdate = async () => {
    if (!this.state.position_name.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณากรอกชื่อตำแหน่ง");
      return;
    } else if (!this.state.user_type.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณาเลือกประเภทพนักงาน");
      return;
    } else if (!this.state.employment_type.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณาเลือกประเภทการจ้างงาน");
      return;
    }

    loading(true);
    let body = {
      position_name: this.state.position_name,
      user_type: this.state.user_type,
      employment_type: this.state.employment_type,
    };
    let result = !this.state.position_id ? await POST("admin/manage/position/create", body) : await PUT("admin/manage/position/update/" + this.state.position_id, body);
    if (result?.status) {
      alert("#", "success");
    }
    loading(false);
  };
  Delete = async (id) => {
    if (await alert("", "warning", "ลบข้อมูล", "ยืนยันการลบรายการนี้หรือไม่ ?", "ลบข้อมูล", "ยกเลิก")) {
      loading(true);
      let result = await DELETE("admin/manage/position/delete/" + id, null);
      if (result?.status) {
        alert("#", "success");
      }
      loading(false);
    }
  };
  Clear = () => {
    this.setState({
      position_id: "",
      position_name: "",
      user_type: "",
      employment_type: "",
    });
  };
  render() {
    return (
      <div className="w-100 min-vh-100">
        <Navbar />
        <div className="p-4">
          <h3 className="mb-3">ตำแหน่่ง</h3>
          <div className="d-flex flex-wrap">
            <input
              type="text"
              className="form-control wpx-250 me-2 mb-2"
              placeholder="ค้นหา..."
              onChange={(e) => {
                this.setState({ search: e.target.value });
              }}
              onKeyUp={(e) => {
                if (e.key === "Enter") {
                  this.Get(1);
                }
              }}
              value={this.state.search}
            />
            <div className="d-flex">
              <button
                className="btn btn-success px-4 me-2 mb-2"
                onClick={() => {
                  this.Get(1);
                }}
              >
                ค้นหา
              </button>
              <button
                className="btn btn-outline-success px-4 me-2 mb-2"
                onClick={() => {
                  window.location.reload();
                }}
              >
                ล้างค่า
              </button>
            </div>
            <button
              className="btn btn-success px-3 ms-auto mb-2"
              onClick={async () => {
                this.Clear();
                this.setState({ modal_create: true });
              }}
            >
              + เพิ่มข้อมูล
            </button>
          </div>
          <div className="table-responsive">
            <table className="table table-borderless rounded-3 overflow-hidden table-hover">
              <thead>
                <tr>
                  <td>ชื่อตำแหน่ง</td>
                  <td>ประเภทพนักงาน</td>
                  <td>ประเภทการจ้างงาน</td>
                  <td className="wpx-80"></td>
                </tr>
              </thead>
              <tbody>
                {this.state.data?.map((item, index) => (
                  <tr>
                    <td>{item.position_name}</td>
                    <td>{item.user_type}</td>
                    <td>{item.employment_type}</td>
                    <td>
                      <div className="d-flex">
                        <BtnIcon
                          type="update"
                          onClick={() => {
                            this.setState({
                              ...item,
                              modal_update: true,
                            });
                          }}
                        />
                        <BtnIcon
                          type="delete"
                          onClick={() => {
                            this.Delete(item?.position_id);
                          }}
                        />
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
          <div className="w-100 d-flex align-items-center justify-content-between flex-wrap mt-2">
            <span className="mb-3">รายการทั้งหมด {this.state.count || 0} รายการ</span>
            <Pagination className="mb-3" count={Math.ceil(this.state.count / 10) || 1} showFirstButton showLastButton onChange={(_, page) => this.Get(page)} />
          </div>
        </div>
        {/* CREATE UPDATE */}
        <Modal show={this.state.modal_create || this.state.modal_update} onHide={() => this.setState({ modal_create: false, modal_update: false })}>
          <Modal.Header closeButton>
            <h5 className="mb-0">{this.state.modal_create ? "เพิ่มข้อมูล" : this.state.modal_update ? "แก้ไขข้อมูล" : ""}</h5>
          </Modal.Header>
          <Modal.Body>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">ชื่อตำแหน่ง</div>
              <input
                type="text"
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  this.setState({ position_name: e.target.value });
                }}
                value={this.state.position_name}
              />
            </div>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">ประเภทพนักงาน</div>
              <Select
                className="form-control"
                options={global.position}
                onChange={(e) => {
                  this.setState({ user_type: e });
                }}
                value={this.state.user_type}
              ></Select>
            </div>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">ประเภทการจ้างงาน</div>
              <Select
                className="form-control"
                options={[
                  { value: "พนักงานประจำ", label: "พนักงานประจำ" },
                  { value: "พนักงานรายวัน", label: "พนักงานรายวัน" },
                ]}
                onChange={(e) => {
                  this.setState({ employment_type: e });
                }}
                value={this.state.employment_type}
              ></Select>
            </div>
          </Modal.Body>
          <Modal.Footer className="py-1 d-flex justify-content-end">
            <button
              className="btn btn-success px-5 me-1"
              onClick={() => {
                this.CreateUpdate();
              }}
            >
              บันทึก
            </button>
            <button
              className="btn btn-secondary px-5 ms-1"
              onClick={() => {
                this.setState({ modal_create: false, modal_update: false });
              }}
            >
              ยกเลิก
            </button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}
