import React, { Component } from "react";
import { profile, branch, alert, total, float, toFixed, format_date, random_charactor, GET, POST, PUT, DELETE, loading, Status, params, DatePicker, Select, BtnIcon, Swal, Pagination, Switch, Navbar, Modal, Resizer } from "../../components/CustomComponent.js";
export default class CategoryDF extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modal_main_create: false,
      modal_main_update: false,
      modal_sub_create: false,
      modal_sub_update: false,

      search_main: "",
      search_sub: "",

      data_main: [],
      count_main: 0,

      data_sub: [],
      data_branch: [],

      main_category_id: "",
      sub_category_id: "",
      category_name: "",
      branch_list: [],
      price: "",

      select_main_category_id: "",
    };
  }

  async componentDidMount() {
    loading(false);
    await this.Get(1);
  }
  Get = async (page) => {
    loading(true);
    let data_main, count_main, data_branch;
    const [result, result1] = await Promise.all([GET("admin/manage/category-doctor-fee/main/list", { page: page, data_search: this.state.search_main }), GET("admin/manage/branch/list", null)]);
    if (result?.status) {
      data_main = result.data;
      count_main = result.count;
    }
    if (result1?.status) {
      data_branch = result1.data;
    }
    this.setState({ data_main, count_main, data_branch });
    loading(false);
  };
  CreateUpdateMain = async () => {
    if (!this.state.category_name.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณากรอกหมวดหมู่ (DF)");
      return;
    }
    loading(true);
    let body = {
      category_name: this.state.category_name,
      branch_list: this.state.branch_list,
    };
    let result = !this.state.main_category_id ? await POST("admin/manage/category-doctor-fee/main/create", body) : await PUT("admin/manage/category-doctor-fee/main/update/" + this.state.main_category_id, body);
    if (result?.status) {
      alert("#", "success");
    }
    loading(false);
  };
  CreateUpdateSub = async () => {
    if (!this.state.category_name.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณากรอกหมวดหมู่ (DF)");
      return;
    }
    loading(true);
    let body = {
      main_category_id: this.state.select_main_category_id,
      category_name: this.state.category_name,
      price: this.state.price,
    };
    let result = !this.state.sub_category_id ? await POST("admin/manage/category-doctor-fee/sub/create", body) : await PUT("admin/manage/category-doctor-fee/sub/update/" + this.state.sub_category_id, body);
    if (result?.status) {
      alert("#", "success");
    }
    loading(false);
  };
  DeleteMain = async (id) => {
    if (await alert("", "warning", "ลบข้อมูล", "ยืนยันการลบรายการนี้หรือไม่ ?", "ลบข้อมูล", "ยกเลิก")) {
      loading(true);
      let result = await DELETE("admin/manage/category-doctor-fee/main/delete/" + id, null);
      if (result?.status) {
        alert("#", "success");
      }
      loading(false);
    }
  };
  DeleteSub = async (id) => {
    if (await alert("", "warning", "ลบข้อมูล", "ยืนยันการลบรายการนี้หรือไม่ ?", "ลบข้อมูล", "ยกเลิก")) {
      loading(true);
      let result = await DELETE("admin/manage/category-doctor-fee/sub/delete/" + id, null);
      if (result?.status) {
        alert("#", "success");
      }
      loading(false);
    }
  };
  render() {
    const {
      modal_main_create,
      modal_main_update,
      modal_sub_create,
      modal_sub_update,
      modal_view,

      search_main,
      search_sub,

      data_main,
      count_main,

      data_sub,
      data_branch,

      main_category_id,
      sub_category_id,
      category_name,
      branch_list,
      price,

      select_main_category_id,
    } = this.state;
    return (
      <div className="w-100 min-vh-100">
        <Navbar />
        <div className="p-4">
          <div className="row">
            <div className="col-12 col-md-6">
              <h3 className="mb-3">หมวดหมู่ (DF)</h3>
              <div className="d-flex flex-wrap">
                <input
                  type="text"
                  className="form-control wpx-250 me-2 mb-2"
                  placeholder="ค้นหา..."
                  onChange={(e) => {
                    this.setState({ search_main: e.target.value });
                  }}
                  onKeyUp={(e) => {
                    if (e.key === "Enter") {
                      this.Get(1);
                    }
                  }}
                  value={search_main}
                />
                <div className="d-flex">
                  <button
                    className="btn btn-success px-4 me-2 mb-2"
                    onClick={() => {
                      this.Get(1);
                    }}
                  >
                    ค้นหา
                  </button>
                  <button
                    className="btn btn-outline-success px-4 me-2 mb-2"
                    onClick={() => {
                      window.location.reload();
                    }}
                  >
                    ล้างค่า
                  </button>
                </div>
                <button
                  className="btn btn-success px-3 ms-auto mb-2"
                  onClick={async () => {
                    this.setState({ modal_main_create: true, main_category_id: "", sub_category_id: "", category_name: "", branch_list: [], price: "" });
                  }}
                >
                  + เพิ่มข้อมูล
                </button>
              </div>
              <div className="table-responsive">
                <table className="table table-borderless rounded-3 overflow-hidden table-hover">
                  <thead>
                    <tr>
                      <td>ชื่อหมวดหมู่หลัก (DF)</td>
                      <td>จำนวนสาขาที่ใช้งาน</td>
                      <td className="wpx-80"></td>
                    </tr>
                  </thead>
                  <tbody>
                    {data_main?.map((item, index) => (
                      <tr
                        onClick={() => {
                          if (select_main_category_id === item.main_category_id) return;
                          this.setState({ select_main_category_id: item.main_category_id, data_sub: item.sub_category_data, count_sub: item.sub_category_data.length });
                        }}
                      >
                        <td>{item.category_name}</td>
                        <td>{item.branch_list.length}</td>
                        <td>
                          <div className="d-flex">
                            <BtnIcon
                              type="update"
                              onClick={() => {
                                this.setState({
                                  ...item,
                                  modal_main_update: true,
                                });
                              }}
                            />
                            <BtnIcon
                              type="delete"
                              onClick={() => {
                                this.DeleteMain(item?.main_category_id);
                              }}
                            />
                          </div>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
              <div className="w-100 d-flex align-items-center justify-content-between flex-wrap mt-2">
                <span className="mb-3">รายการทั้งหมด {count_main || 0} รายการ</span>
                <Pagination className="mb-3" count={Math.ceil(count_main / 10) || 1} showFirstButton showLastButton onChange={(_, page) => this.Get(page)} />
              </div>
            </div>
            <div className="col-12 col-md-6">
              <h3 className="mb-3">{data_main.find((e) => e.main_category_id === select_main_category_id)?.category_name || "ยังไม่ได้เลือกหมวดหมู่ (DF)"}</h3>
              {select_main_category_id && (
                <div>
                  <div className="d-flex flex-wrap">
                    <input
                      type="text"
                      className="form-control wpx-250 me-2 mb-2"
                      placeholder="ค้นหา..."
                      onChange={(e) => {
                        this.setState({ search_sub: e.target.value });
                      }}
                      value={search_sub}
                    />
                    <div className="d-flex">
                      <button className="btn btn-success px-4 me-2 mb-2" onClick={() => {}}>
                        ค้นหา
                      </button>
                      <button
                        className="btn btn-outline-success px-4 me-2 mb-2"
                        onClick={() => {
                          window.location.reload();
                        }}
                      >
                        ล้างค่า
                      </button>
                    </div>
                    <button
                      className="btn btn-success px-3 ms-auto mb-2"
                      onClick={async () => {
                        this.setState({ modal_sub_create: true, sub_category_id: "", category_name: "", branch_list: [], price: "" });
                      }}
                    >
                      + เพิ่มข้อมูล
                    </button>
                  </div>
                  <div className="table-responsive">
                    <table className="table table-borderless rounded-3 overflow-hidden table-hover">
                      <thead>
                        <tr>
                          <td>ชื่อหมวดหมู่รอง (DF)</td>
                          <td>ค่า DF</td>
                          <td className="wpx-80"></td>
                        </tr>
                      </thead>
                      <tbody>
                        {data_sub?.filter(item => !search_sub || item.category_name.includes(search_sub)).map((item, index) => (
                          <tr>
                            <td>{item.category_name}</td>
                            <td>{item.price}</td>
                            <td>
                              <div className="d-flex">
                                <BtnIcon
                                  type="update"
                                  onClick={() => {
                                    this.setState({
                                      ...item,
                                      modal_sub_update: true,
                                    });
                                  }}
                                />
                                <BtnIcon
                                  type="delete"
                                  onClick={() => {
                                    this.DeleteSub(item?.sub_category_id);
                                  }}
                                />
                              </div>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
        {/* CREATE UPDATE */}
        <Modal show={modal_main_create || modal_main_update} onHide={() => this.setState({ modal_main_create: false, modal_main_update: false })}>
          <Modal.Header closeButton>
            <h5 className="mb-0">{modal_main_create ? "เพิ่มหมวดหมู่ (DF)" : modal_main_update ? "แก้ไขหมวดหมู่ (DF)" : ""}</h5>
          </Modal.Header>
          <Modal.Body>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">หมวดหมู่ (DF)</div>
              <input
                type="text"
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  this.setState({ category_name: e.target.value });
                }}
                value={category_name}
              />
            </div>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">สาขา</div>
              <div className="row">
                {this.state.data_branch?.map((item, index) => (
                  <div className="col-6 d-flex align-items-start" key={"role_" + index + "_" + index}>
                    <input
                      type="checkbox"
                      className="mt-1 me-2"
                      onChange={(e) => {
                        let branch_list = this.state.branch_list;
                        if (branch_list.includes(item.id)) {
                          branch_list.splice(
                            branch_list.findIndex((ob) => ob === item.id),
                            1
                          );
                        } else {
                          branch_list.push(item.id);
                        }
                        this.setState({ branch_list: branch_list });
                      }}
                      checked={this.state.branch_list.includes(item.id)}
                    />
                    {item.branch_name}
                  </div>
                ))}
              </div>
            </div>
          </Modal.Body>
          <Modal.Footer className="py-1 d-flex justify-content-end">
            <button
              className="btn btn-success px-5 me-1"
              onClick={() => {
                this.CreateUpdateMain();
              }}
            >
              บันทึก
            </button>
            <button
              className="btn btn-secondary px-5 ms-1"
              onClick={() => {
                this.setState({ modal_main_create: false, modal_main_update: false });
              }}
            >
              ยกเลิก
            </button>
          </Modal.Footer>
        </Modal>
        {/* CREATE UPDATE */}
        <Modal show={modal_sub_create || modal_sub_update} onHide={() => this.setState({ modal_sub_create: false, modal_sub_update: false })}>
          <Modal.Header closeButton>
            <h5 className="mb-0">{modal_sub_create ? "เพิ่มข้อมูล" : modal_sub_update ? "แก้ไขข้อมูล" : ""}</h5>
          </Modal.Header>
          <Modal.Body>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">หมวดหมู่หลัก (DF)</div>
              <input type="text" className="form-control" placeholder="กรอกข้อมูล..." value={data_main.find((e) => e.main_category_id === select_main_category_id)?.category_name || "ยังไม่ได้เลือกหมวดหมู่ (DF)"} disabled />
            </div>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">หมวดหมู่ (DF)</div>
              <input
                type="text"
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  this.setState({ category_name: e.target.value });
                }}
                value={category_name}
              />
            </div>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">ค่า DF</div>
              <input
                type="text"
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  if(!float(e.target.value)) return;
                  this.setState({ price: e.target.value });
                }}
                value={price}
              />
            </div>
          </Modal.Body>
          <Modal.Footer className="py-1 d-flex justify-content-end">
            <button
              className="btn btn-success px-5 me-1"
              onClick={() => {
                this.CreateUpdateSub();
              }}
            >
              บันทึก
            </button>
            <button
              className="btn btn-secondary px-5 ms-1"
              onClick={() => {
                this.setState({ modal_sub_create: false, modal_sub_update: false });
              }}
            >
              ยกเลิก
            </button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}
