import React, { Component } from "react";
import { profile, branch, alert, total, float, toFixed, format_date, random_charactor, GET, POST, PUT, DELETE, loading, Status, params, DatePicker, Select, BtnIcon, Swal, Pagination, Switch, Navbar, Modal, Resizer }from "../../../components/CustomComponent.js";
export default class ListCourse extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modal_create: false,
      modal_update: false,
      modal_view: false,

      search: "",

      data: [],
      count: 0,

      position_id: "",
      position_name: "",
      user_type: "",
      employment_type: "",
    };
  }

  async componentDidMount() {
    loading(false);
    await this.Get(1);
  }
  Get = async (page) => {
    loading(true);
    let result = await GET("admin/manage/course/list", { page: page, data_search: this.state.search });
    if (result?.status) {
      this.setState({ data: result.data, count: result.count });
    }
    loading(false);
  };
  Delete = async (id) => {
    if (await alert("", "warning", "ลบข้อมูล", "ยืนยันการลบรายการนี้หรือไม่ ?", "ลบข้อมูล", "ยกเลิก")) {
      loading(true);
      let result = await DELETE("admin/manage/course/delete/" + id, null);
      if (result?.status) {
        alert("#", "success");
      }
      loading(false);
    }
  };
  render() {
    return (
      <div className="w-100 min-vh-100">
        <Navbar />
        <div className="p-4">
          <h3 className="mb-3">รายการคอร์ส</h3>
          <div className="d-flex flex-wrap">
            <input
              type="text"
              className="form-control wpx-250 me-2 mb-2"
              placeholder="ค้นหา..."
              onChange={(e) => {
                this.setState({ search: e.target.value });
              }}
              onKeyUp={(e) => {
                if (e.key === "Enter") {
                  this.Get(1);
                }
              }}
              value={this.state.search}
            />
            <div className="d-flex">
              <button
                className="btn btn-success px-4 me-2 mb-2"
                onClick={() => {
                  this.Get(1);
                }}
              >
                ค้นหา
              </button>
              <button
                className="btn btn-outline-success px-4 me-2 mb-2"
                onClick={() => {
                  window.location.reload();
                }}
              >
                ล้างค่า
              </button>
            </div>
            <button
              className="btn btn-success px-3 ms-auto mb-2"
              onClick={async () => {
                window.location.href = "/admin/course/add-course";
              }}
            >
              + เพิ่มข้อมูล
            </button>
          </div>
          <div className="table-responsive">
            <table className="table table-borderless rounded-3 overflow-hidden table-hover">
              <thead>
                <tr>
                  <td>คอร์ส</td>
                  <td>สินค้า</td>
                  <td>หมวดหมู่</td>
                  <td>จำนวนวัน</td>
                  <td>ทำซ้ำ</td>
                  <td>ราคา</td>
                  <td>DF</td>
                  <td>สถานะ</td>
                  <td className="wpx-80"></td>
                </tr>
              </thead>
              <tbody>
                {this.state.data?.map((item, index) => (
                  <tr>
                    <td><span>{item.course_name}</span><br/><span className="text-success">{item.course_type}</span></td>
                    <td><ul className="list-unstyled">
                      <li>{item.product_data?.product_name}</li>
                      {item.other_product_list.map(e => (<li>{e.product_name}</li>))}
                      {item.service_list.map(e => (<li>{item.product_name}</li>))}
                      </ul></td>
                    <td>{item.category_data?.category_name}</td>
                    <td>{item.duration_day}</td>
                    <td>{item.repeat_duration_day}</td>
                    <td>{toFixed(item.price)}</td>
                    <td>{item.is_doctor_fee ? Status("มี","success") : Status("ไม่มี","danger")}</td>
                    <td>{item.is_active ? Status("ใช้งาน","success") : Status("ไม่ใช้งาน","danger")}</td>
                    <td>
                      <div className="d-flex">
                        <BtnIcon
                          type="update"
                          onClick={() => {
                            window.location.href = "/admin/course/update-course?id=" + item.course_id;
                          }}
                        />
                        <BtnIcon
                          type="delete"
                          onClick={() => {
                            this.Delete(item?.course_id);
                          }}
                        />
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
          <div className="w-100 d-flex align-items-center justify-content-between flex-wrap mt-2">
            <span className="mb-3">รายการทั้งหมด {this.state.count || 0} รายการ</span>
            <Pagination className="mb-3" count={Math.ceil(this.state.count / 10) || 1} showFirstButton showLastButton onChange={(_, page) => this.Get(page)} />
          </div>
        </div>
      </div>
    );
  }
}
