import React, { Component } from "react";
import { profile, branch, alert, total, float, toFixed, format_date, random_charactor, GET, POST, PUT, DELETE, loading, Status, params, DatePicker, Select, BtnIcon, Swal, Pagination, Switch, Navbar, Modal, Resizer } from "../../../components/CustomComponent.js";
export default class Leaveday extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modal_create: false,
      modal_update: false,
      modal_view: false,

      search: "",
      search_user_id: "",
      search_leave_status: "",

      data: [],
      count: 0,

      data_employee: [],

      leave_id: "",
      user_id: "",
      leave_date: "",
      leave_type: "",
      remark: "",
      leave_status: 0,
    };
  }

  async componentDidMount() {
    loading(false);
    await this.Get(1);
  }
  Get = async (page) => {
    loading(true);
    let data, count, data_employee;
    const [result1, result2] = await Promise.all([GET("admin/manage/leave-day/list", { page: page, data_search: this.state.search, user_id: this.state.search_user_id, leave_status: this.state.search_leave_status }), GET("admin/manage/employee/list", null)]);
    if (result1?.status) {
      data = result1.data;
      count = result1.count;
    }
    if (result2?.status) {
      data_employee = result2.data.map((e) => {
        return { value: e.user_id, label: `${e.first_name} ${e.last_name} (${e.nickname})` };
      });
    }
    this.setState({ data, count, data_employee });
    loading(false);
  };
  CreateUpdate = async () => {
    if (!this.state.user_id.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณาเลือกพนักงาน");
      return;
    } else if (!this.state.leave_date.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณาเลือกวันลา");
      return;
    } else if (!this.state.leave_type.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณาเลือกประเภทการลา");
      return;
    }

    loading(true);
    let body = {
      ...this.state,
    };
    let result = !this.state.leave_id ? await POST("admin/manage/leave-day/create", body) : await PUT("admin/manage/leave-day/update/" + this.state.leave_id, body);
    if (result?.status) {
      alert("#", "success");
    }
    loading(false);
  };
  UpdateAll = async (status) => {
    loading(true);
    const selectedItems = this.state.data.filter((e) => e.selected);
    const updatePromises = selectedItems.map((e) => PUT(`api/admin/manage/leave-day/update/${e.leave_id}`, { ...e, leave_status: status }));
    let result = await Promise.all(updatePromises);
    if (result.filter((e) => e.status).length === result.length) {
      alert("#", "success");
    }
    loading(false);
  };
  Clear = () => {
    this.setState({
      leave_id: "",
      user_id: "",
      leave_date: "",
      leave_type: "",
      remark: "",
      leave_status: 0,
    });
  };
  render() {
    return (
      <div className="w-100 min-vh-100">
        <Navbar />
        <div className="p-4">
          <h3 className="mb-3">รายการวันลา</h3>
          <div className="d-flex flex-wrap">
            <Select
              className="form-control wpx-200 me-2 mb-2"
              placeholder="พนักงาน"
              options={this.state.data_employee}
              onChange={(e) => {
                this.setState({ search_user_id: e });
              }}
              value={this.state.search_user_id}
            ></Select>
            <div className="wpx-200 me-2 mb-2">
              <DatePicker
                className="form-control"
                onChange={(e) => {
                  this.setState({ search: e });
                }}
                value={this.state.search}
              />
            </div>
            <Select
              className="form-control wpx-200 me-2 mb-2"
              placeholder="สถานะ"
              options={global.ez_status}
              onChange={(e) => {
                this.setState({ search_leave_status: e });
              }}
              value={this.state.search_leave_status}
            ></Select>
            <div className="d-flex">
              <button
                className="btn btn-success px-4 me-2 mb-2"
                onClick={() => {
                  this.Get(1);
                }}
              >
                ค้นหา
              </button>
              <button
                className="btn btn-outline-success px-4 me-2 mb-2"
                onClick={() => {
                  window.location.reload();
                }}
              >
                ล้างค่า
              </button>
            </div>
            <button
              className="btn btn-success px-3 ms-auto mb-2"
              onClick={async () => {
                this.Clear();
                this.setState({ modal_create: true });
              }}
            >
              + เพิ่มข้อมูล
            </button>
          </div>
          <div className="table-responsive">
            <table className="table table-borderless rounded-3 overflow-hidden table-hover">
              <thead>
                <tr>
                  <td>
                    <input
                      type="checkbox"
                      onChange={(e) => {
                        let data = this.state.data;
                        data = data.map((obj) => {
                          return { ...obj, selected: obj.leave_status !== 4 ? e.target.checked : obj.selected };
                        });
                        this.setState({ data });
                      }}
                      checked={this.state.data.filter((e) => e.selected).length === this.state.data.length}
                    />
                  </td>
                  <td>พนักงาน</td>
                  <td>วันที่ลา</td>
                  <td>ประเภทการลา</td>
                  <td>หมายเหตุ</td>
                  <td>สถานะ</td>
                </tr>
              </thead>
              <tbody>
                {this.state.data?.map((item, index) => (
                  <tr>
                    <td>
                      <input
                        type="checkbox"
                        onChange={() => {
                          let data = this.state.data;
                          data[index].selected = !data[index].selected;
                          this.setState({ data });
                        }}
                        checked={item.selected}
                      />
                    </td>
                    <td>{this.state.data_employee.find((e) => e.value == item.user_id)?.label}</td>
                    <td>{format_date(item.leave_date)}</td>
                    <td>{item.leave_type}</td>
                    <td>{item.remark}</td>
                    <td>{item.leave_status === 1 ? Status("รออนุมัติ", "warning") : item.leave_status === 2 ? Status("อนุมัติ", "success") : item.leave_status === 3 ? Status("ไม่อนุมัติ", "danger") : Status("ยกเลิก", "secondary")}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
          <div className="w-100 d-flex align-items-center justify-content-between flex-wrap mt-2">
            <span className="mb-3">รายการทั้งหมด {this.state.count || 0} รายการ</span>
            <Pagination className="mb-3" count={Math.ceil(this.state.count / 10) || 1} showFirstButton showLastButton onChange={(_, page) => this.Get(page)} />
          </div>
          {this.state.data && this.state.data.length !== 0 && (
            <div className="py-1 d-flex justify-content-center mt-3">
              <button
                className="btn btn-success px-5 me-1"
                onClick={() => {
                  this.UpdateAll(2);
                }}
              >
                อนุมัติ
              </button>
              <button
                className="btn btn-danger px-5 ms-1"
                onClick={() => {
                  this.UpdateAll(3);
                }}
              >
                ไม่อนุมัติ
              </button>
              <button
                className="btn btn-secondary px-5 ms-1"
                onClick={() => {
                  this.UpdateAll(4);
                }}
              >
                ยกเลิก
              </button>
            </div>
          )}
        </div>
        {/* CREATE UPDATE */}
        <Modal show={this.state.modal_create || this.state.modal_update} onHide={() => this.setState({ modal_create: false, modal_update: false })}>
          <Modal.Header closeButton>
            <h5 className="mb-0">{this.state.modal_create ? "เพิ่มข้อมูล" : this.state.modal_update ? "แก้ไขข้อมูล" : ""}</h5>
          </Modal.Header>
          <Modal.Body>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">พนักงาน</div>
              <Select
                className="form-control"
                options={this.state.data_employee}
                onChange={(e) => {
                  this.setState({ user_id: e });
                }}
                value={this.state.user_id}
              ></Select>
            </div>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">วันที่ลา</div>
              <DatePicker
                className="form-control"
                onChange={(e) => {
                  this.setState({ leave_date: e });
                }}
                value={this.state.leave_date}
              />
            </div>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">ประเภทการลา</div>
              <Select
                className="form-control"
                options={global.leaveday}
                onChange={(e) => {
                  this.setState({ leave_type: e });
                }}
                value={this.state.leave_type}
              ></Select>
            </div>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">หมายเหตุ</div>
              <textarea
                className="form-control"
                placeholder="กรอกข้อมูล..."
                rows={5}
                onChange={(e) => {
                  this.setState({ remark: e.target.value });
                }}
                value={this.state.remark}
              />
            </div>
          </Modal.Body>
          <Modal.Footer className="py-1 d-flex justify-content-end">
            <button
              className="btn btn-success px-5 me-1"
              onClick={() => {
                this.CreateUpdate();
              }}
            >
              บันทึก
            </button>
            <button
              className="btn btn-secondary px-5 ms-1"
              onClick={() => {
                this.setState({ modal_create: false, modal_update: false });
              }}
            >
              ยกเลิก
            </button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}
