import React, { Component } from "react";
import { profile, branch, alert, total, float, toFixed, format_date, random_charactor, GET, POST, PUT, DELETE, loading, Status, params, DatePicker, Select, BtnIcon, Swal, Pagination, Switch, Navbar, Modal, Resizer } from "../../../components/CustomComponent.js";
export default class OtherExpenses extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modal_create: false,
      modal_update: false,

      search: "",

      data: [],
      count: 0,

      data_emp: [],

      name: "", // ชื่อรายการ
      document_number: "", // เลขที่เอกสาร
      type: "", // ประเภท
      is_pay_employee: true, // จ่ายพนักงาน
      user_id: "", // พนักงาน
      amount: "", // จำนวนเงิน
      date: "", // วันที่
      remark: "", // หมายเหตุ
      image: "", // รูปภาพ
    };
  }

  async componentDidMount() {
    loading(false);
    await this.Get(1);
  }
  Get = async (page) => {
    loading(true);
    let data_emp, data, count;
    const [result1, result2] = await Promise.all([GET("admin/manage/employee/list", { page: 0, data_search: "" }), GET("admin/manage/expenses/list", { page: page, data_search: this.state.search })]);
        if (result1?.status) {
          data_emp = result1.data.map(e => {return {value:e.user_id, label:e.first_name + " " + e.last_name}});
        }
        if (result2?.status) {
          data = result2.data;
          count = result2.count;
        }
        this.setState({ data_emp, data, count });
    loading(false);
  };
  CreateUpdate = async () => {
    if (!this.state.name.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณากรอกชื่อรายการ");
      return;
    } else if (!this.state.document_number.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณากรอกเลขที่บิล/ใบเสร็จ/PO");
      return;
    } else if (!this.state.type.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณาเลือกประเภทรายจ่าย");
      return;
    } else if (!this.state.amount.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณาเลือกจำนวนเงิน");
      return;
    } else if (!this.state.date.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณาเลือกวันที่");
      return;
    } else if (!this.state.image.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณาเพิ่มรูปภาพใบเสร็จ");
      return;
    }

    loading(true);
    let body = { ...this.state };
    let result = !this.state.expenses_id ? await POST("admin/manage/expenses/create", body) : await PUT("admin/manage/expenses/update/" + this.state.expenses_id, body);
    if (result?.status) {
      alert("#", "success");
    }
    loading(false);
  };
  Delete = async (id) => {
    if (await alert("", "warning", "ลบข้อมูล", "ยืนยันการลบรายการนี้หรือไม่ ?", "ลบข้อมูล", "ยกเลิก")) {
      loading(true);
      let result = await DELETE("admin/manage/expenses/delete/" + id, null);
      if (result?.status) {
        alert("#", "success");
      }
      loading(false);
    }
  };
  Clear = () => {
    this.setState({
      position_id: "",
      position_name: "",
      user_type: "",
      employment_type: "",
    });
  };
  render() {
    const { search, data, count, data_emp, modal_create, modal_update, name, document_number, type, is_pay_employee, user_id, amount, date, remark, image } = this.state;
    return (
      <div className="w-100 min-vh-100">
        <Navbar />
        <div className="p-4">
          <h3 className="mb-3">รายจ่ายอื่นๆ</h3>
          <div className="d-flex flex-wrap">
            <input
              type="text"
              className="form-control wpx-250 me-2 mb-2"
              placeholder="ค้นหา..."
              onChange={(e) => {
                this.setState({ search: e.target.value });
              }}
              onKeyUp={(e) => {
                if (e.key === "Enter") {
                  this.Get(1);
                }
              }}
              value={search}
            />
            <div className="d-flex">
              <button
                className="btn btn-success px-4 me-2 mb-2"
                onClick={() => {
                  this.Get(1);
                }}
              >
                ค้นหา
              </button>
              <button
                className="btn btn-outline-success px-4 me-2 mb-2"
                onClick={() => {
                  window.location.reload();
                }}
              >
                ล้างค่า
              </button>
            </div>
            <button
              className="btn btn-success px-3 ms-auto mb-2"
              onClick={async () => {
                this.Clear();
                this.setState({ modal_create: true, name: "", document_number: "", type: "", is_pay_employee: true, user_id: "", amount: "", date: "", remark: "", image: "" });
              }}
            >
              + เพิ่มข้อมูล
            </button>
          </div>
          <div className="table-responsive">
            <table className="table table-borderless rounded-3 overflow-hidden table-hover">
              <thead>
                <tr>
                  <td>ชื่อรายการ</td>
                  <td>ประเภทรายจ่าย</td>
                  <td>พนักงาน</td>
                  <td>วันที่</td>
                  <td>จำนวนเงิน</td>
                  <td className="wpx-80"></td>
                </tr>
              </thead>
              <tbody>
                {data?.map((item, index) => (
                  <tr>
                    <td>
                      {item.name}
                      <br />
                      {item.remark}
                    </td>
                    <td>{item.type}</td>
                    <td>
                      <span>{item.user_data?.first_name || ""} {item.user_data?.last_name || ""}</span>
                      <br />
                      <span className="text-success">{item.is_pay_employee}</span>
                    </td>
                    <td>{format_date(item.date)}</td>
                    <td>{toFixed(item.amount)}</td>
                    <td>
                      <div className="d-flex">
                        <BtnIcon
                          type="update"
                          onClick={() => {
                            this.setState({
                              ...item,
                              user_id: item.user_data?.user_id || "",
                              modal_update: true,
                            });
                          }}
                        />
                        <BtnIcon
                          type="delete"
                          onClick={() => {
                            this.Delete(item?.expenses_id);
                          }}
                        />
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
          <div className="w-100 d-flex align-items-center justify-content-between flex-wrap mt-2">
            <span className="mb-3">รายการทั้งหมด {count || 0} รายการ</span>
            <Pagination className="mb-3" count={Math.ceil(count / 10) || 1} showFirstButton showLastButton onChange={(_, page) => this.Get(page)} />
          </div>
        </div>
        {/* CREATE UPDATE */}
        <Modal show={modal_create || modal_update} onHide={() => this.setState({ modal_create: false, modal_update: false })}>
          <Modal.Header closeButton>
            <h5 className="mb-0">{modal_create ? "เพิ่มรายจ่ายอื่นๆ" : modal_update ? "แก้ไขรายจ่ายอื่นๆ" : ""}</h5>
          </Modal.Header>
          <Modal.Body>
            <div className="d-flex mb-3">
              <div className="wpx-120 mt-2">ชื่อรายการ</div>
              <input
                type="text"
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  this.setState({ name: e.target.value });
                }}
                value={name}
              />
            </div>
            <div className="d-flex mb-3">
              <div className="wpx-120 mt-2">เลขที่บิล/ใบเสร็จ/PO</div>
              <input
                type="text"
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  this.setState({ document_number: e.target.value });
                }}
                value={document_number}
              />
            </div>
            <div className="d-flex mb-3">
              <div className="wpx-120 mt-2">ประเภทรายจ่าย</div>
               <Select
                  className="form-control"
                  options={["ค่านั่ง", "เงินเดือน", "แพทย์", "อื่นๆ"].map(e => {return {value:e, label:e}})}
                  onChange={(e) => {
                    this.setState({ type: e });
                  }}
                  value={type}
                ></Select>
            </div>
            <div className="d-flex mb-3">
              <input
                type="checkbox"
                className="me-2"
                onChange={(e) => {
                  this.setState({ is_pay_employee: !is_pay_employee, user_id: "" });
                }}
                checked={is_pay_employee}
              />
              <span>จ่ายให้พนักงาน</span>
            </div>
            {is_pay_employee && (
              <div className="d-flex mb-2">
                <div className="wpx-120 mt-2">พนักงาน</div>
                <Select
                  className="form-control"
                  options={data_emp}
                  onChange={(e) => {
                    this.setState({ user_id: e });
                  }}
                  value={user_id}
                ></Select>
              </div>
            )}
            <div className="d-flex mb-3">
              <div className="wpx-120 mt-2">จำนวนเงิน</div>
              <input
                type="text"
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  if (!float(e.target.value)) {
                    return;
                  }
                  this.setState({ amount: e.target.value });
                }}
                value={amount}
              />
            </div>
            <div className="d-flex mb-3">
              <div className="wpx-120 mt-2">จำนวนเงิน</div>
              <DatePicker
                className="form-control"
                onChange={(e) => {
                  this.setState({ date: e });
                }}
                value={date}
              />
            </div>
            <div className="d-flex mb-3">
              <div className="wpx-120 mt-2">หมายเหตุ</div>
              <textarea
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  this.setState({ remark: e.target.value });
                }}
                value={remark}
              />
            </div>
            <div className="d-flex mb-3">
              <div className="wpx-120 mt-2">ใบเสร็จ</div>
              <div className="d-flex flex-wrap">
                <input
                  hidden
                  accept="image/*"
                  id="image"
                  type="file"
                  onChange={async (e) => {
                    if (e.target.files[0]) {
                      let image = await new Promise((resolve) => {
                        Resizer.imageFileResizer(
                          e.target.files[0],
                          1200,
                          1200,
                          "JPEG",
                          100,
                          0,
                          (uri) => {
                            resolve(uri);
                          },
                          "base64"
                        );
                      });
                      this.setState({ image: image });
                      e.target.value = "";
                    }
                  }}
                />
                {image ? (
                  <div className="wpx-100 hpx-100 rounded-3 border p-1 position-relative">
                    <img alt="example" src={image} className="rounded-3" style={{ width: "100%", height: "100%", objectFit: "cover" }} />
                    <span
                      className="wpx-20 hpx-20 rounded-circle bg-danger pointer text-white text-16 position-absolute icon d-flex align-items-center justify-content-center shadow"
                      style={{ right: -8, top: -8 }}
                      onClick={() => {
                        this.setState({ image: "" });
                      }}
                    >
                      {"\uf00d"}
                    </span>
                  </div>
                ) : (
                  <div className="d-flex">
                    <div
                      className="wpx-100 hpx-100 d-flex align-items-center justify-content-center icon text-32 text-success bg-success-light rounded-3 pointer"
                      onClick={() => {
                        document.getElementById("image").click();
                      }}
                    >
                      {"\uf03e"}
                    </div>
                    <small className="text-secondary ps-3">
                      <b>ขนาดไฟล์ที่แนะนำ:</b> ไม่เกิน 1200x1200 พิกเซล เพื่อให้ได้คุณภาพที่ดีที่สุด <br />
                      <b>รูปแบบไฟล์ที่รองรับ:</b> PNG, JPG, JPEG, GIF <br />
                      <b>ขนาดไฟล์สูงสุด:</b> 5MB
                    </small>
                  </div>
                )}
              </div>
            </div>
          </Modal.Body>
          <Modal.Footer className="py-1 d-flex justify-content-end">
            <button
              className="btn btn-success px-5 me-1"
              onClick={() => {
                this.CreateUpdate();
              }}
            >
              บันทึก
            </button>
            <button
              className="btn btn-secondary px-5 ms-1"
              onClick={() => {
                this.setState({ modal_create: false, modal_update: false });
              }}
            >
              ยกเลิก
            </button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}
