import React, { Component } from "react";
import { profile, branch, alert, total, float, toFixed, format_date, random_charactor, GET, POST, PUT, DELETE, loading, Status, params, DatePicker, Select, BtnIcon, Swal, Pagination, Switch, Navbar, Modal, Resizer } from "../../../components/CustomComponent.js";
const array_time = ["08.00", "08.30", "09.00", "09.30", "10.00", "10.30", "11.00", "11.30", "12.00", "12.30", "13.00", "13.30", "14.00", "14.30", "15.00", "15.30", "16.00", "16.30", "17.00", "17.30", "18.00", "18.30", "19.00", "19.30", "20.00", "20.30", "21.00", "21.30", "22.00", "22.30", "23.00", "23.30"].map((e) => {
  return { value: e, label: e };
});
export default class Position extends Component {
  constructor(props) {
    super(props);
    this.state = {
      search_user_id: "",

      remark_leave: "",

      data_employee: [],
      data_position: [],

      // วันหยุด
      data_leaveday: [],

      // วันงาน
      data_job: [],

      array_day: [],
      current_date: new Date(),
    };
    this.textareaRef = React.createRef();
  }

  async componentDidMount() {
    loading(false);
    await this.getWeeksInMonth(0);
    this.adjustHeight();
  }

  componentDidUpdate() {
    // ปรับขนาดเมื่อ state ถูกอัปเดต
    this.adjustHeight();
  }

  adjustHeight = () => {
    const textarea = this.textareaRef.current;
    if (textarea) {
      textarea.style.height = "auto"; // รีเซ็ตความสูง
      textarea.style.height = `${textarea.scrollHeight}px`; // ปรับความสูง
    }
  };

  Get = async (page) => {
    loading(true);
    let data_leaveday, data_job, data_position, data_employee, remark_leave;
    const [result1, result2, result3, result4, result5] = await Promise.all([GET("admin/manage/leave-day/list", { page: 0, user_id: this.state.search_user_id, leave_status: 2, year_month: format_date(this.state.current_date, "yyyy-mm", "en") }), GET("admin/manage/daily-job/list", { page: 0, user_id: this.state.search_user_id, daily_id: "", year_month: format_date(this.state.current_date, "yyyy-mm", "en") }), GET("admin/manage/position/list", null), GET("admin/manage/employee/list", null), GET("admin/manage/branch/list", { page: 0, branch_id: branch?.branch_id })]);
    if (result1?.status) {
      data_leaveday = result1.data;
    }
    if (result2?.status) {
      data_job = result2.data;
    }
    if (result3?.status) {
      data_position = result3.data.map((e) => ({
        value: e.position_id,
        label: `${e.position_name}`,
        user_type: e.user_type,
      }));
    }
    if (result4?.status) {
      data_employee = result4.data
        .filter((e) => e.user_type === "แพทย์")
        .map((e) => {
          return { value: e.user_id, label: `${e.first_name} ${e.last_name} (${e.nickname})`, ...e, position: data_position.find((p) => p.value === e.position)?.label };
        })
        .sort((a, b) => {
          if (a.position < b.position) return -1;
          if (a.position > b.position) return 1;
          return 0;
        });
    }
    if (result5?.status) {
      remark_leave = result5.data?.remark_leave || "";
    }
    console.log("data_employee:", data_employee);
    console.log("data_job:", data_job);
    this.setState({
      data_leaveday,
      data_job,
      data_position,
      data_employee,
      remark_leave,
    });
    loading(false);
  };
  getWeeksInMonth = (type) => {
    const weeks = [];
    let week = [];
    let current_date = this.state.current_date;
    console.log("current_date:", current_date);
    let date = new Date(current_date.getFullYear(), current_date.getMonth() + (type || 0), 1);

    // ปรับวันเริ่มต้นของสัปดาห์เป็นวันจันทร์
    let start_day = new Date(date).getDay() === 0 ? 6 : new Date(date).getDay() - 1;

    let end_date = new Date(date.getFullYear(), date.getMonth() + 1, 0).getDate();
    let day = 1;

    for (let i = 0; i < 6; i++) {
      for (let j = 0; j < 7; j++) {
        if (i === 0 && j < start_day) {
          week.push(null);
        } else if (day > end_date) {
          week.push(null);
        } else {
          week.push(day);
          day++;
        }
      }
      if (week.filter((item) => item === null).length !== 7) {
        weeks.push(week);
        week = [];
      }
    }

    this.setState({ array_day: weeks, current_date: date, data_job: [], data_leaveday: [] });
    setTimeout(() => {
      this.Get(0);
    }, 10);
  };

  render() {
    return (
      <div className="w-100 min-vh-100">
        <Navbar />
        <div className="p-4">
          <h3 className="mb-3">ตารางเวลาแพทย์</h3>
          <div className="card rounded-3 border-0 shadow-lg p-3">
            <div className="d-flex justify-content-between mb-3">
              <div className="d-flex">
                <h4 className="text-nowrap me-4">{format_date(this.state.current_date, "mmmm yyyy")}</h4>
                <span
                  className="icon wpx-30 hpx-30 rounded-circle bg-success-light d-flex align-items-center justify-content-center ms-2 pointer"
                  onClick={() => {
                    this.getWeeksInMonth(-1);
                  }}
                >
                  {"\uf053"}
                </span>
                <span
                  className="icon wpx-30 hpx-30 rounded-circle bg-success-light d-flex align-items-center justify-content-center ms-2 pointer"
                  onClick={() => {
                    this.getWeeksInMonth(1);
                  }}
                >
                  {"\uf054"}
                </span>
              </div>
              <Select
                className="form-control wpx-200"
                placeholder="เลือกแพทย์"
                options={this.state.data_employee}
                onChange={(e, obj) => {
                  this.setState({ search_user_id: e, data_job: [], data_leaveday: [], data_overtime: [] });
                  setTimeout(() => {
                    this.Get(0);
                  }, 10);
                }}
                value={this.state.search_user_id}
                default={false}
              />
            </div>
            <div className="table-responsive">
              <table className="table">
                <tr className="bg-primary-light">
                  <td className="p-1 border-1" style={{ minWidth: 140 }}>
                    ชื่อสกุล
                  </td>
                  <td className="p-1 border-1" style={{ minWidth: 140 }}>
                    ชื่อเล่น
                  </td>
                  <td className="p-1 border-1" style={{ minWidth: 140 }}>
                    ตำแหน่ง
                  </td>
                  <td className="p-1 border-1" style={{ minWidth: 140 }}>
                    เบอร์โทร
                  </td>
                  {this.state.array_day.map((week) => {
                    return week.map((item, index) => item && <td className={index >= 5 ? "text-center wpx-30 p-1 border-1 bg-danger-light" : "text-center wpx-30 p-1 border-1"}>{item}</td>);
                  })}
                </tr>
                {this.state.data_employee
                  .filter((emp) => !this.state.search_user_id || emp.user_id == this.state.search_user_id)
                  .map((emp, emp_index) => (
                    <tr>
                      <td className="p-1 border-1">{emp.first_name}</td>
                      <td className="p-1 border-1">{emp.nickname}</td>
                      <td className="p-1 border-1">{emp.position}</td>
                      <td className="p-1 border-1">{emp.phone}</td>
                      {this.state.array_day.map((week) => {
                        return week.map((item, index) => item && <td className={this.state.data_leaveday.find((e) => e.user_id === emp.user_id && format_date(e.leave_date, "d") == item) ? "text-center wpx-30 p-1 border-1 bg-danger" : emp["s_" + global.day_en[index]] || this.state.data_job.find((e) => e.user_id === emp.user_id && format_date(e.record_date, "d") == item) ? "text-center wpx-30 p-1 border-1 bg-success" : index >= 5 ? "text-center wpx-30 p-1 border-1 bg-danger-light" : "text-center wpx-30 p-1 border-1"}></td>);
                      })}
                    </tr>
                  ))}
              </table>
            </div>
            <textarea
        ref={this.textareaRef}
        value={this.state.remark_leave}
        id="custom-textarea"
        disabled
      />
          </div>
        </div>
      </div>
    );
  }
}
