import React, { Component } from "react";
import { profile, branch, alert, total, float, toFixed, format_date, random_charactor, GET, POST, PUT, DELETE, loading, Status, params, DatePicker, Select, BtnIcon, Swal, Pagination, Switch, Navbar, Modal, Resizer } from "../../../components/CustomComponent.js";
export default class SearchCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modal_create: false,
      modal_update: false,
      modal_view: false,

      search: "",

      data: [],
      count: 0,

      position_id: "",
      position_name: "",
      user_type: "",
      employment_type: "",
    };
  }

  async componentDidMount() {
    loading(false);
    await this.Get(1);
  }
  Get = async (page) => {
    loading(true);
    let result = await GET("admin/manage/customer/list-shot", { page: page, data_search: this.state.search });
    if (result?.status) {
      this.setState({ data: result.data, count: result.count });
    }
    loading(false);
  };
  render() {
    return (
      <div className="w-100 min-vh-100">
        <Navbar />
        <div className="p-4">
          <h3 className="mb-3">ค้นหาลูกค้า</h3>
          <div className="d-flex flex-wrap mb-3">
            <input
              type="text"
              className="form-control wpx-250 me-2 mb-2"
              placeholder="ค้นหา..."
              onChange={(e) => {
                this.setState({ search: e.target.value });
              }}
              onKeyUp={(e) => {
                if (e.key === "Enter") {
                  this.Get(1);
                }
              }}
              value={this.state.search}
            />
            <div className="d-flex me-3">
              <button
                className="btn btn-success px-4 me-2 mb-2"
                onClick={() => {
                  this.Get(1);
                }}
              >
                ค้นหา
              </button>
              <button
                className="btn btn-outline-success px-4 me-2 mb-2"
                onClick={() => {
                  window.location.reload();
                }}
              >
                ล้างค่า
              </button>
            </div>
            <button
              className="btn btn-success px-3 mb-2 ms-auto"
              onClick={async () => {
                window.location.href = "/admin/customer/create-customer";
              }}
            >
              + เพิ่มข้อมูล
            </button>
          </div>
          <div className="row">
            {this.state.data?.map((item, index) => (
              <div className="col-12 col-md-6 mb-3">
                <div className="card p-3 rounded-3 border-0 shadow">
                  <div className="d-flex justify-content-between">
                    <div className="d-flex">
                      <div className="me-3 text-center">
                        <img className="rounded-circle" alt="example" src={item.picture ? item.picture : require(`../../../assets/images/profile/${(index % 30) + 1}.png`)} style={{ width: 60, height: 60, objectFit: "cover" }} />
                        <div className="bg-success text-white px-3 rounded-pill mt-2">ลูกค้าทั่วไป</div>
                      </div>
                      <div>
                        <div>
                          <div class="d-flex">
                            {item.customer_code} 
                            {/* <div className="bg-secondary-light px-2 rounded-pill ms-1">ความงาม 2</div> */}
                          </div>
                          <span>
                            {item.firstname} {item.lastname} ({item.nickname})
                          </span>{" "}
                          <br />
                          <div>
                            <span className="icon text-success me-2">{"\uf095"}</span>
                            {item.phone || "-"}
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="wpx-150">
                      <button
                        className="btn btn-success mb-2 w-100"
                        onClick={() => {
                          window.location.href = "/admin/customer/view-customer?id=" + item?.customer_id;
                        }}
                      >
                        ดูรายละเอียด
                      </button>
                      <button className="btn btn-primary mb-3 w-100" onClick={() => {}}>
                        บันทึกการรักษา
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
          <div className="w-100 d-flex align-items-center justify-content-between flex-wrap mt-2">
            <span className="mb-3">รายการทั้งหมด {this.state.count || 0} รายการ</span>
            <Pagination className="mb-3" count={Math.ceil(this.state.count / 10) || 1} showFirstButton showLastButton onChange={(_, page) => this.Get(page)} />
          </div>
        </div>
        {/* CREATE UPDATE */}
        <Modal show={this.state.modal_create || this.state.modal_update} onHide={() => this.setState({ modal_create: false, modal_update: false })}>
          <Modal.Header closeButton>
            <h5 className="mb-0">{this.state.modal_create ? "เพิ่มข้อมูล" : this.state.modal_update ? "แก้ไขข้อมูล" : ""}</h5>
          </Modal.Header>
          <Modal.Body>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">ชื่อตำแหน่ง</div>
              <input
                type="text"
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  this.setState({ position_name: e.target.value });
                }}
                value={this.state.position_name}
              />
            </div>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">ประเภทพนักงาน</div>
              <Select
                className="form-control"
                options={global.position}
                onChange={(e) => {
                  this.setState({ user_type: e });
                }}
                value={this.state.user_type}
              ></Select>
            </div>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">ประเภทการจ้างงาน</div>
              <Select
                className="form-control"
                options={[
                  { value: "พนักงานประจำ", label: "พนักงานประจำ" },
                  { value: "พนักงานรายวัน", label: "พนักงานรายวัน" },
                ]}
                onChange={(e) => {
                  this.setState({ employment_type: e });
                }}
                value={this.state.employment_type}
              ></Select>
            </div>
          </Modal.Body>
          <Modal.Footer className="py-1 d-flex justify-content-end">
            <button
              className="btn btn-success px-5 me-1"
              onClick={() => {
                this.CreateUpdate();
              }}
            >
              บันทึก
            </button>
            <button
              className="btn btn-secondary px-5 ms-1"
              onClick={() => {
                this.setState({ modal_create: false, modal_update: false });
              }}
            >
              ยกเลิก
            </button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}
