import React, { Component } from "react";
import { profile, branch, alert, total, float, toFixed, format_date, random_charactor, GET, POST, PUT, DELETE, loading, Status, params, DatePicker, Select, BtnIcon, Swal, Pagination, Switch, Navbar, Modal, Resizer } from "../../../components/CustomComponent.js";
export default class Payroll extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modal_select: false,

      modal_view: false,
      modal_create: false,

      modal_user_update: false,
      modal_user_view: false,

      search: "",

      data: [],
      payroll_list: [],
      count: 0,

      employment_type: "",
      payment_channel: "",

      // รายการ
      payroll_id: "",
      record_date: "",
      start_date: "",
      end_date: "",
      payment_date: "",

      // ผู้ใช้
      select_index: -1,

      user_id: "",
      salary: 0,
      social_insurance: 0,
      withholding_tax: 0,
      total_expenses: 0,
      total_net: 0,
      addon: [],
      reduce: [],
      leave: [],
    };
  }

  async componentDidMount() {
    loading(false);
    await this.Get(1);
  }
  Get = async (page) => {
    loading(true);
    let result = await GET("admin/manage/payroll/list", { page: page, payroll_id: "", payment_date: this.state.search });
    if (result?.status) {
      console.log("result.data:", result.data);
      this.setState({ data: result.data, count: result.count });
    }
    loading(false);
  };
  GetByID = async (id) => {
    loading(true);
    let result1 = await GET("admin/manage/payroll/list", { page: 0, payroll_id: id, payment_date: "" });
    let result2 = await GET("admin/manage/leave-day/list", { page: 0, user_id: "", leave_status: 2, year_month: format_date(result1.data.end_date, "yyyy-mm", "en") });
    if (result1?.status) {
      let item = result1.data;
      console.log("item:", item);
      console.log("result2.data:", result2.data);
      console.log(
        item.payroll_list.map((obj) => {
          return {
            ...obj,
            leave: global.leaveday.map((e) => {
              return { name: e.label, day: result2.data.filter((l) => l.user_id === e.user_id && l.leave_type === e.label).length };
            }),
          };
        })
      );
      this.setState({
        payroll_id: item.payroll_id,
        record_date: item.record_date,
        start_date: item.start_date,
        end_date: item.end_date,
        payment_date: item.payment_date,
        payroll_list: item.payroll_list.map((obj) => {
          return {
            ...obj,
            leave: global.leaveday.map((g) => {
              return { name: g.label, day: result2.data.filter((l) => l.user_id === obj.user_id && l.leave_type === g.label).length };
            }),
          };
        }),
      });
    }
    loading(false);
  };
  GetEmployee = async () => {
    loading(true);
    const [result1, result2] = await Promise.all([GET("admin/manage/employee/list-payroll", { employment_type: this.state.employment_type, payment_channel: this.state.payment_channel }), GET("admin/manage/leave-day/list", { page: 0, user_id: "", leave_status: 2, year_month: format_date(this.state.end_date, "yyyy-mm", "en") })]);
    if (result1?.status) {
      console.log(result1.data);
      console.log(result2.data);
      this.setState({
        payroll_list: result1.data.map((e) => {
          let social_insurance = e.social_insurance ? (e.salary * 0.05 > 750 ? 750 : e.salary * 0.05) : 0;
          return {
            user_id: e.user_id,
            prefix_name: e.prefix_name,
            first_name: e.first_name,
            last_name: e.last_name,
            payment_type: e.payment_channel,
            salary: e.salary,
            social_insurance: social_insurance,
            withholding_tax: e.withholding_tax,
            total_expenses: Number(e.salary) + Number(e.ot_wages) + Number(e.df_wages) + Number(e.sit_wages) - (Number(social_insurance) + Number(e.withholding_tax)),
            total_net: Number(e.salary) + Number(e.ot_wages) + Number(e.df_wages) + Number(e.sit_wages) - (Number(social_insurance) + Number(e.withholding_tax)),
            addon: [
              { name: "ค่าล่วงเวลา", price: e.ot_wages },
              { name: "คอมมิชชัน", price: 0 },
              { name: "ค่า DF", price: e.df_wages },
              { name: "ค่านั่งแพทย์", price: e.sit_wages },
            ],
            reduce: [{ name: "ขาด/ลา", price: 0 }],
            leave: global.leaveday.map((g) => {
              return { name: g.label, day: result2.data.filter((l) => l.user_id === e.user_id && l.leave_type === g.label).length };
            }),
          };
        }),
        record_date: format_date(new Date(), "yyyy-mm-dd", "en"),
        payment_date: format_date(new Date(), "yyyy-mm-dd", "en"),
        modal_select: false,
        modal_create: true,
      });
    }
    loading(false);
  };
  CreateUpdate = async () => {
    loading(true);
    let body = {
      start_date: this.state.start_date,
      end_date: this.state.end_date,
      payment_date: this.state.payment_date,
      total_expenses: Number(total(this.state.payroll_list, "total_expenses")),
      total_net: Number(total(this.state.payroll_list, "total_net")),
      payroll_list: this.state.payroll_list,
    };
    let result = !this.state.payroll_id ? await POST("admin/manage/payroll/create", body) : await PUT("admin/manage/payroll/update/" + this.state.payroll_id, body);
    if (result?.status) {
      alert("#", "success");
    }
    loading(false);
  };
  UpdateStatus = async (status) => {
    loading(true);
    let body = {
      payroll_status: status,
    };
    let result = await PUT("admin/manage/payroll/update/" + this.state.payroll_id, body);
    if (result?.status) {
      alert("#", "success");
    }
    loading(false);
  };
  CreateUpdateUser = async () => {
    loading(true);
    let payroll_list = this.state.payroll_list;
    payroll_list[this.state.select_index] = {
      ...payroll_list[this.state.select_index],
      salary: this.state.salary,
      social_insurance: this.state.social_insurance,
      withholding_tax: this.state.withholding_tax,
      total_expenses: Number(this.state.salary) + Number(total(this.state.addon, "price")) - (Number(this.state.social_insurance) + Number(this.state.withholding_tax) + Number(total(this.state.reduce, "price"))),
      total_net: Number(this.state.salary) + Number(total(this.state.addon, "price")) - (Number(this.state.social_insurance) + Number(this.state.withholding_tax) + Number(total(this.state.reduce, "price"))),
      addon: this.state.addon,
      reduce: this.state.reduce,
      leave: this.state.leave,
    };
    this.setState({
      payroll_list: payroll_list,
      modal_create: true,
      modal_user_update: false,
    });
    loading(false);
  };
  Delete = async (id) => {
    if (await alert("", "warning", "ลบข้อมูล", "ยืนยันการลบรายการนี้หรือไม่ ?", "ลบข้อมูล", "ยกเลิก")) {
      loading(true);
      let result = await DELETE("admin/manage/position/delete/" + id, null);
      if (result?.status) {
        alert("#", "success");
      }
      loading(false);
    }
  };
  Clear = () => {
    this.setState({
      position_id: "",
      position_name: "",
      employment_type: "",
    });
  };
  render() {
    return (
      <div className="w-100 min-vh-100">
        <Navbar />
        <div className="p-4">
          <h3 className="mb-3">รายการจ่ายคอมมิชชัน</h3>
          <div className="d-flex flex-wrap">
            <div className="wpx-200 me-2 mb-2">
              <DatePicker
                className="form-control"
                onChange={(e) => {
                  this.setState({ search: e });
                }}
                value={this.state.search}
              />
            </div>
            <div className="d-flex">
              <button
                className="btn btn-success px-4 me-2 mb-2"
                onClick={() => {
                  this.Get(1);
                }}
              >
                ค้นหา
              </button>
              <button
                className="btn btn-outline-success px-4 me-2 mb-2"
                onClick={() => {
                  window.location.reload();
                }}
              >
                ล้างค่า
              </button>
            </div>
            <button
              className="btn btn-success px-3 ms-auto mb-2"
              onClick={async () => {
                this.setState({
                  modal_select: true,
                  employment_type: "พนักงานประจำ",
                  payment_channel: "โอนเงิน",
                  start_date: format_date(new Date(new Date().getFullYear(), new Date().getMonth() - 1, 1), "yyyy-mm-dd", "en"),
                  end_date: format_date(new Date(new Date().getFullYear(), new Date().getMonth(), 0), "yyyy-mm-dd", "en"),
                });
              }}
            >
              + เพิ่มข้อมูล
            </button>
          </div>
          <div className="table-responsive">
            <table className="table table-borderless rounded-3 overflow-hidden table-hover">
              <thead>
                <tr>
                  <td>ช่วงวันที่</td>
                  <td>วันที่จ่าย</td>
                  <td>ค่าใช้จ่ายรวม</td>
                  <td>ยอดจ่ายรวมสุทธิ</td>
                  <td>สถานะ</td>
                  <td></td>
                </tr>
              </thead>
              <tbody>
                {this.state.data?.map((item, index) => (
                  <tr>
                    <td>{format_date(item.start_date) + " - " + format_date(item.end_date)}</td>
                    <td>{format_date(item.payment_date)}</td>
                    <td>{item.total_expenses}</td>
                    <td>{item.total_net}</td>
                    <td>{item.payroll_status === 1 ? Status("รอตรวจสอบ", "warning") : item.payroll_status === 2 ? Status("อนุมัติ", "success") : item.payroll_status === 3 ? Status("ไม่อนุมัติ", "danger") : Status("ยกเลิก", "secondary")}</td>
                    <td>
                      <div className="d-flex">
                        <BtnIcon
                          type="view"
                          onClick={async () => {
                            await this.GetByID(item.payroll_id);
                            this.setState({ modal_view: true });
                          }}
                        />
                        {item.payroll_status === 1 && (
                          <BtnIcon
                            type="update"
                            onClick={async () => {
                              await this.GetByID(item.payroll_id);
                              this.setState({ modal_update: true });
                            }}
                          />
                        )}
                        <BtnIcon
                          type="download"
                          onClick={() => {
                            Swal.fire({
                              icon: "warning",
                              title: "ยืนยันการจ่ายคอมมิชชัน",
                              text: "จ่ายคอมมิชชัน “" + item.slip_number + "” หรือไม่?",
                              confirmButtonText: "ยืนยันการจ่ายคอมมิชชัน",
                              showDenyButton: true,
                              denyButtonText: "ยกเลิกรายการ",
                              showCancelButton: true,
                              cancelButtonText: "ปิดหน้าต่าง",
                            }).then((result) => {
                              if (result.isConfirmed) {
                                this.UpdateStatus(2);
                              } else if (result.isDenied) {
                                this.UpdateStatus(3);
                              }
                            });
                          }}
                        />
                        <BtnIcon
                          type="email"
                          onClick={() => {
                            this.setState({ modal_view: true });
                          }}
                        />
                        {item.payroll_status === 1 && (
                          <BtnIcon
                            type="approve"
                            onClick={() => {
                              this.setState({ modal_view: true });
                            }}
                          />
                        )}
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
          <div className="w-100 d-flex align-items-center justify-content-between flex-wrap mt-2">
            <span className="mb-3">รายการทั้งหมด {this.state.count || 0} รายการ</span>
            <Pagination className="mb-3" count={Math.ceil(this.state.count / 10) || 1} showFirstButton showLastButton onChange={(_, page) => this.Get(page)} />
          </div>
        </div>

        {/* SELECT */}
        <Modal show={this.state.modal_select} onHide={() => this.setState({ modal_select: false })}>
          <Modal.Header closeButton>
            <h4 className="mb-0">สร้างรายการคอมมิชชัน</h4>
          </Modal.Header>
          <Modal.Body>
            <label className="mb-2">ประเภทพนักงาน</label>
            <div className="d-flex mb-3">
              <button
                className={`btn ${this.state.employment_type === "พนักงานประจำ" ? "btn-success" : "btn-light"} w-100 hpx-60 me-1`}
                onClick={() => {
                  this.setState({ employment_type: "พนักงานประจำ" });
                }}
              >
                พนักงานรายเดือน
              </button>
              <button
                className={`btn ${this.state.employment_type === "พนักงานรายวัน" ? "btn-success" : "btn-light"} w-100 hpx-60 ms-1`}
                onClick={() => {
                  this.setState({ employment_type: "พนักงานรายวัน" });
                }}
              >
                พนักงานรายวัน
              </button>
            </div>
            <label className="mb-2">ช่องทางชำระ</label>
            <div className="d-flex mb-3">
              <button
                className={`btn ${this.state.payment_channel === "โอนเงิน" ? "btn-success" : "btn-light"} w-100 hpx-60 me-1`}
                onClick={() => {
                  this.setState({ payment_channel: "โอนเงิน" });
                }}
              >
                โอนผ่านธนาคาร
              </button>
              <button
                className={`btn ${this.state.payment_channel === "เงินสด" ? "btn-success" : "btn-light"} w-100 hpx-60 ms-1`}
                onClick={() => {
                  this.setState({ payment_channel: "เงินสด" });
                }}
              >
                เงินสด
              </button>
            </div>
            <div className="mb-3">
              <div className="wpx-120 mt-2">ตั้งแต่วันที่</div>
              <DatePicker
                className="form-control"
                onChange={(e) => {
                  this.setState({ start_date: e });
                }}
                value={this.state.start_date}
              />
            </div>
            <div className="mb-3">
              <div className="wpx-120 mt-2">ถึงวันที่</div>
              <DatePicker
                className="form-control"
                onChange={(e) => {
                  this.setState({ end_date: e });
                }}
                value={this.state.end_date}
              />
            </div>
            <hr />
            <div className="d-flex justify-content-end mb-2">
              <button
                className="btn btn-warning px-4 me-1"
                onClick={() => {
                  if (!this.state.start_date || !this.state.end_date) {
                    alert("", "warning", "แจ้งเตือน", "กรุณาเลือกวันเริ่มต้น-สิ้นสุด");
                    return;
                  }
                  this.GetEmployee();
                }}
              >
                สร้างรายการคอมมิชชัน
              </button>
              <button
                className="btn btn-secondary px-4 ms-1"
                onClick={() => {
                  this.setState({ modal_select: true });
                }}
              >
                ยกเลิก
              </button>
            </div>
          </Modal.Body>
        </Modal>
        {/* CREATE UPDATE */}
        <Modal show={this.state.modal_create || this.state.modal_update || this.state.modal_view} onHide={() => this.setState({ modal_create: false, modal_update: false, modal_view: false })} fullscreen>
          <Modal.Header closeButton>
            <h5 className="mb-0">{this.state.modal_create ? "สร้างรานการคอมมิชชัน" : this.state.modal_update ? "แก้ไขข้อมูล" : "รายละเอียด"}</h5>
          </Modal.Header>
          <Modal.Body>
            <div className="row mb-3">
              <div className="col-12 col-md-6 mb-3">
                <div className="row">
                  <div className="col-12 col-md-6">
                    <div className="wpx-120 mt-2">วันที่ทำรายการ</div>
                    <DatePicker
                      className="form-control"
                      onChange={(e) => {
                        this.setState({ record_date: e });
                      }}
                      value={this.state.record_date}
                      disabled
                    />
                  </div>
                  <div className="col-12 col-md-6"></div>
                  <div className="col-12 col-md-6">
                    <div className="wpx-120 mt-2">ตั้งแต่วันที่</div>
                    <DatePicker
                      className="form-control"
                      onChange={(e) => {
                        this.setState({ start_date: e });
                      }}
                      value={this.state.start_date}
                      disabled
                    />
                  </div>
                  <div className="col-12 col-md-6">
                    <div className="wpx-120 mt-2">ถึงวันที่</div>
                    <DatePicker
                      className="form-control"
                      onChange={(e) => {
                        this.setState({ end_date: e });
                      }}
                      value={this.state.end_date}
                      disabled
                    />
                  </div>
                  <div className="col-12 col-md-6">
                    <div className="wpx-120 mt-2">วันที่ชำระ</div>
                    <DatePicker
                      className="form-control"
                      onChange={(e) => {
                        this.setState({ payment_date: e });
                      }}
                      value={this.state.payment_date}
                      disabled={this.state.modal_view}
                    />
                  </div>
                  <div className="col-12 col-md-6">
                    <div className="wpx-120 mt-2">ประกันสังคม</div>
                    <input
                      type="text"
                      className="form-control"
                      placeholder="กรอกข้อมูล..."
                      onChange={(e) => {
                        this.setState({ text: e.target.value });
                      }}
                      value={this.state.text}
                    />
                  </div>
                </div>
              </div>
              <div className="col-12 col-md-6 d-flex justify-content-end flex-wrap">
                <div className="px-2">
                  <div>รวมค่าใช้จ่ายพนักงาน</div>
                  <h1 className="fw-bold text-success">{toFixed(total(this.state.payroll_list, "total_expenses"))}</h1>
                </div>
                <div className="px-2">
                  <div>รวมจ่ายสุทธิ</div>
                  <h1 className="fw-bold text-success">{toFixed(total(this.state.payroll_list, "total_net"))}</h1>
                </div>
              </div>
            </div>

            <div className="table-responsive">
              <table className="table table-borderless rounded-3 overflow-hidden table-hover">
                <thead>
                  <tr>
                    <td>ลำดับ</td>
                    <td>ชื่อ-นามสกุล</td>
                    <td>ช่องทางชำระ</td>
                    <td>คอมมิชชัน</td>
                    <td>รายการปรับเพิ่มลด</td>
                    <td>ยอดจ่ายสุทธิ</td>
                    <td className="wpx-80"></td>
                  </tr>
                </thead>
                <tbody>
                  {this.state.payroll_list?.map((item, index) => (
                    <tr>
                      <td>{index + 1}</td>
                      <td>{item.first_name + " " + item.last_name}</td>
                      <td>{item.payment_type}</td>
                      <td>{"0.00"}</td>
                      <td>
                        {
                          <div>
                            <div>
                              {toFixed(total(item.addon, "price"))}
                              <span className="text-12 icon text-success bg-success-light rounded px-1 py-1 ms-1">{"\uf062"}</span>
                            </div>
                            <div>
                              {toFixed(total(item.reduce, "price"))}
                              <span className="text-12 icon text-danger bg-danger-light rounded px-1 py-1 ms-1">{"\uf063"}</span>
                            </div>
                          </div>
                        }
                      </td>
                      <td>{toFixed(item.total_net)}</td>
                      <td>
                        <div className="d-flex">
                          {this.state.modal_view ? (
                            <BtnIcon
                              type="view"
                              onClick={async () => {
                                this.setState({ modal_view: false, modal_user_view: true, ...item, select_index: index });
                              }}
                            />
                          ) : (
                            <BtnIcon
                              type="update"
                              onClick={async () => {
                                this.setState({ modal_create: false, modal_update: false, modal_user_update: true, ...item, select_index: index });
                              }}
                            />
                          )}
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </Modal.Body>
          {(this.state.modal_create || this.state.modal_update) && (
            <Modal.Footer className="py-1 d-flex justify-content-end">
              <button
                className="btn btn-success px-5 me-1"
                onClick={() => {
                  this.CreateUpdate();
                }}
              >
                บันทึก
              </button>
              <button
                className="btn btn-secondary px-5 ms-1"
                onClick={() => {
                  this.setState({ modal_create: false });
                }}
              >
                ยกเลิก
              </button>
            </Modal.Footer>
          )}
          {this.state.modal_view && (
            <Modal.Footer className="py-1 d-flex justify-content-end">
              <button
                className="btn btn-secondary px-5 ms-1"
                onClick={() => {
                  this.setState({ modal_view: false });
                }}
              >
                ปิดหน้าต่าง
              </button>
            </Modal.Footer>
          )}
        </Modal>
        {/* CREATE UPDATE USER */}
        <Modal show={this.state.modal_user_update || this.state.modal_user_view} onHide={() => this.setState({ modal_user_update: false, modal_user_view: false })}>
          <Modal.Header closeButton>
            <h5 className="mb-0">ชื่อจริง นามสกุลจริง</h5>
          </Modal.Header>
          <Modal.Body>
            <h5>ข้อมูลเงินเดือน/ค่าจ้าง</h5>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">เงินเดือน</div>
              <input
                type="text"
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  this.setState({ salary: e.target.value });
                }}
                value={this.state.salary}
                disabled={this.state.modal_user_view}
              />
            </div>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">ประกันสังคม</div>
              <input
                type="text"
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  this.setState({ social_insurance: e.target.value });
                }}
                value={this.state.social_insurance}
                disabled={this.state.modal_user_view}
              />
            </div>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">หักภาษี ณ ที่จ่าย</div>
              <input
                type="text"
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  this.setState({ withholding_tax: e.target.value });
                }}
                value={this.state.withholding_tax}
                disabled={this.state.modal_user_view}
              />
            </div>
            <hr />
            <div className="w-100 d-flex align-items-center justify-content-between mb-2">
              <h5 className="text-success">รายการปรับเพิ่ม</h5>
              {this.state.modal_user_update && (
                <button
                  className="btn btn-success px-4"
                  onClick={() => {
                    this.setState({ addon: [...this.state.addon, { label: "", price: "" }] });
                  }}
                >
                  เพิ่มรายการเพิ่มเงิน
                </button>
              )}
            </div>
            {this.state.addon.map((item, index) => (
              <div className="d-flex align-items-center mb-2">
                {index >= 4 ? (
                  <input
                    type="text"
                    className="form-control wpx-110"
                    placeholder="ค่าอื่นๆ..."
                    onChange={(e) => {
                      let addon = this.state.addon;
                      addon[index].name = e.target.value;
                      this.setState({ addon: addon });
                    }}
                    value={item.name}
                    disabled={this.state.modal_user_view}
                  />
                ) : (
                  <label className="wpx-110">{item.name}</label>
                )}
                <div className="wpx-10"></div>
                <input
                  type="text"
                  className="form-control"
                  placeholder="จำนวนเงิน..."
                  onChange={(e) => {
                    let addon = this.state.addon;
                    addon[index].price = e.target.value;
                    this.setState({ addon: addon });
                  }}
                  value={item.price}
                  disabled={this.state.modal_user_view}
                />
                {index >= 4 && (
                  <div
                    className="status bg-danger-light text-danger ms-2"
                    onClick={async () => {
                      let addon = this.state.addon;
                      addon.splice(index, 1);
                      this.setState({ addon: addon });
                    }}
                  >
                    {"\uf1f8"}
                  </div>
                )}
              </div>
            ))}
            <hr />
            <div className="w-100 d-flex align-items-center justify-content-between mb-2">
              <h5 className="text-danger">รายการปรับลด</h5>
              {this.state.modal_user_update && (
                <button
                  className="btn btn-danger px-4"
                  onClick={() => {
                    this.setState({ reduce: [...this.state.reduce, { label: "", price: "" }] });
                  }}
                >
                  เพิ่มรายการลดเงิน
                </button>
              )}
            </div>
            <div className="p-3 bg-primary-light rounded-3 mb-3">
              <div className="row">
                {this.state.leave.map((item, index) => (
                  <div className="col-6 d-flex justify-content-between">
                    <label>{item.name}</label>
                    <div className="d-flex">
                      <label>{item.day || 0}</label>
                      <label className="wpx-30 text-end">วัน</label>
                    </div>
                  </div>
                ))}
              </div>
            </div>
            {this.state.reduce.map((item, index) => (
              <div className="d-flex align-items-center mb-2">
                {index >= 1 ? (
                  <input
                    type="text"
                    className="form-control wpx-110"
                    placeholder="ค่าอื่นๆ..."
                    onChange={(e) => {
                      let reduce = this.state.reduce;
                      reduce[index].name = e.target.value;
                      this.setState({ reduce: reduce });
                    }}
                    value={item.name}
                    disabled={this.state.modal_user_view}
                  />
                ) : (
                  <label className="wpx-110">{item.name}</label>
                )}
                <div className="wpx-10"></div>
                <input
                  type="text"
                  className="form-control"
                  placeholder="จำนวนเงิน..."
                  onChange={(e) => {
                    let reduce = this.state.reduce;
                    reduce[index].price = e.target.value;
                    this.setState({ reduce: reduce });
                  }}
                  value={item.price}
                  disabled={this.state.modal_user_view}
                />
                {index >= 1 && (
                  <div
                    className="status bg-danger-light text-danger ms-2"
                    onClick={async () => {
                      let reduce = this.state.reduce;
                      reduce.splice(index, 1);
                      this.setState({ reduce: reduce });
                    }}
                  >
                    {"\uf1f8"}
                  </div>
                )}
              </div>
            ))}
            <hr />
            <div className="d-flex justify-content-between">
              <label>รวมทั้งสิ้น</label>
              <h3 className="fw-bold">{toFixed(Number(this.state.salary) + Number(total(this.state.addon, "price")) - (Number(this.state.social_insurance) + Number(this.state.withholding_tax) + Number(total(this.state.reduce, "price"))))}</h3>
            </div>
          </Modal.Body>
          {this.state.modal_user_update && (
            <Modal.Footer className="py-1 d-flex justify-content-end">
              <button
                className="btn btn-success px-5 me-1"
                onClick={() => {
                  this.CreateUpdateUser();
                }}
              >
                บันทึก
              </button>
              <button
                className="btn btn-secondary px-5 ms-1"
                onClick={() => {
                  this.setState({ modal_create: true, modal_user_update: false });
                }}
              >
                ยกเลิก
              </button>
            </Modal.Footer>
          )}
          {this.state.modal_user_view && (
            <Modal.Footer className="py-1 d-flex justify-content-end">
              <button
                className="btn btn-secondary px-5 ms-1"
                onClick={() => {
                  this.setState({ modal_view: true, modal_user_view: false });
                }}
              >
                ปิดหน้าต่าง
              </button>
            </Modal.Footer>
          )}
        </Modal>
      </div>
    );
  }
}
