import React, { Component } from "react";
import { profile, branch, alert, total, float, toFixed, format_date, random_charactor, GET, POST, PUT, DELETE, loading, Status, params, DatePicker, Select, BtnIcon, Swal, Pagination, Switch, Navbar, Modal, Resizer } from "../../../components/CustomComponent.js";
export default class NoLeaveday extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modal_create: false,
      modal_update: false,
      modal_view: false,

      search: "",

      data: [],
      count: 0,

      no_leave_id: "",
      date: "",
      remark: "",
    };
  }

  async componentDidMount() {
    loading(false);
    await this.Get(1);
  }
  Get = async (page) => {
    loading(true);
    let result = await GET("admin/manage/noleave-day/list", {
      page: page,
      data_search: this.state.search,
    });
    if (result?.status) {
      console.log(result.data);
      this.setState({ data: result.data, count: result.count });
    }
    loading(false);
  };
  CreateUpdate = async () => {
    if (!this.state.date.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณาเลือกวันห้ามลา");
      return;
    }

    loading(true);
    let body = {
      date: this.state.date,
      remark: this.state.remark,
    };
    let result = !this.state.no_leave_id ? await POST("admin/manage/noleave-day/create", body) : await PUT("admin/manage/noleave-day/update/" + this.state.no_leave_id, body);
    if (result?.status) {
      alert("#", "success");
    }
    loading(false);
  };
  Delete = async (id) => {
    if (await alert("", "warning", "ลบข้อมูล", "ยืนยันการลบรายการนี้หรือไม่ ?", "ลบข้อมูล", "ยกเลิก")) {
      loading(true);
      let result = await DELETE("admin/manage/noleave-day/delete/" + id, null);
      if (result?.status) {
        alert("#", "success");
      }
      loading(false);
    }
  };
  Clear = () => {
    this.setState({
      no_leave_id: "",
      date: "",
      remark: "",
    });
  };
  render() {
    return (
      <div className="w-100 min-vh-100">
        <Navbar />
        <div className="p-4">
          <h3 className="mb-3">รายการวันห้ามลา</h3>
          <div className="d-flex flex-wrap">
            <div className="wpx-200 me-2 mb-2">
              <DatePicker
                className="form-control"
                onChange={(e) => {
                  this.setState({ search: e });
                }}
                value={this.state.search}
              />
            </div>
            <div className="d-flex">
              <button
                className="btn btn-success px-4 me-2 mb-2"
                onClick={() => {
                  this.Get(1);
                }}
              >
                ค้นหา
              </button>
              <button
                className="btn btn-outline-success px-4 me-2 mb-2"
                onClick={() => {
                  window.location.reload();
                }}
              >
                ล้างค่า
              </button>
            </div>
            <button
              className="btn btn-success px-3 ms-auto mb-2"
              onClick={async () => {
                this.Clear();
                this.setState({ modal_create: true });
              }}
            >
              + เพิ่มข้อมูล
            </button>
          </div>

          <div className="table-responsive">
            <table className="table table-borderless rounded-3 overflow-hidden table-hover">
              <thead>
                <tr>
                  <td>วันที่ห้ามลา</td>
                  <td>หมายเหตุ</td>
                  <td className="wpx-50"></td>
                </tr>
              </thead>
              <tbody>
                {this.state.data?.map((item, index) => (
                  <tr>
                    <td>{format_date(item.date)}</td>
                    <td>{item.remark}</td>
                    <td>
                      <BtnIcon
                        type="delete"
                        onClick={() => {
                          this.Delete(item.no_leave_id);
                        }}
                      />
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
          <div className="w-100 d-flex align-items-center justify-content-between flex-wrap mt-2">
            <span className="mb-3">รายการทั้งหมด {this.state.count || 0} รายการ</span>
            <Pagination className="mb-3" count={Math.ceil(this.state.count / 10) || 1} showFirstButton showLastButton onChange={(_, page) => this.Get(page)} />
          </div>
        </div>
        {/* CREATE UPDATE */}
        <Modal show={this.state.modal_create || this.state.modal_update} onHide={() => this.setState({ modal_create: false, modal_update: false })}>
          <Modal.Header closeButton>
            <h5 className="mb-0">{this.state.modal_create ? "เพิ่มข้อมูล" : this.state.modal_update ? "แก้ไขข้อมูล" : ""}</h5>
          </Modal.Header>
          <Modal.Body>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">วันที่ห้ามลา</div>
              <DatePicker
                className="form-control"
                onChange={(e) => {
                  this.setState({ date: e });
                }}
                value={this.state.date}
              />
            </div>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">หมายเหตุ</div>
              <textarea
                className="form-control"
                placeholder="กรอกข้อมูล..."
                rows={5}
                onChange={(e) => {
                  this.setState({ remark: e.target.value });
                }}
                value={this.state.remark}
              />
            </div>
          </Modal.Body>
          <Modal.Footer className="py-1 d-flex justify-content-end">
            <button
              className="btn btn-success px-5 me-1"
              onClick={() => {
                this.CreateUpdate();
              }}
            >
              บันทึก
            </button>
            <button
              className="btn btn-secondary px-5 ms-1"
              onClick={() => {
                this.setState({ modal_create: false, modal_update: false });
              }}
            >
              ยกเลิก
            </button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}
