import React, { Component } from "react";
import { profile, branch, alert, total, float, toFixed, format_date, random_charactor, GET, POST, PUT, DELETE, loading, Status, params, DatePicker, Select, BtnIcon, Swal, Pagination, Switch, Navbar, Modal, Resizer } from "../../../components/CustomComponent.js";
export default class Product extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modal_create: false,
      modal_update: false,

      search: "",

      data: [],
      data_category: [],
      data_unit: [],
      count: 0,

      product_id: "",
      product_name: "", // ชื่อสินค้า
      product_type: "", // ประเภทสินค้า
      category_id: "", // หมวดหมู่สินค้าหลัก
      unit_id: "", // หน่วยนับ
      profit_percent: "", // กำไร %
      minimum_price: "", // ราคาขายขั้นต่ำ (บาท/หน่วย)
      front_price: "", // ราคาขายหน้าร้าน (บาท/หน่วย)
      median_price: "", // ราคาขายกลาง (บาท/หน่วย)
      notify_expire: "", // จำนวนวันแจ้งเตือนก่อนหมดอายุ
      safety_stock: "", // จำนวนแจ้งเตือนเมื่อใกล้หมดสต็อค
      limit_stock: "", // จำนวนสูงสุดที่ควรสต็อค
    };
  }

  async componentDidMount() {
    loading(false);
    await this.Get(1);
  }
  Get = async (page) => {
    loading(true);
    let result = await GET("admin/manage/product/list", { page: page, data_search: this.state.search });
    if (result?.status) {
      this.setState({ data: result.data, count: result.count });
    }
    let data, count, data_category, data_unit;
    const [result1, result2, result3] = await Promise.all([GET("admin/manage/product/list", { page: page, data_search: this.state.search }), GET("admin/manage/category/list", null), GET("admin/manage/product-unit/list", null)]);
    if (result1?.status) {
      data = result1.data;
      count = result1.count;
    }
    if (result2?.status) {
      data_category = result2.data.map((e) => ({
        value: e.category_id,
        label: e.category_name,
      }));
    }
    if (result3?.status) {
      data_unit = result3.data.map((e) => ({
        value: e.unit_id,
        label: e.unit_name,
      }));
    }
    this.setState({ data, count, data_category, data_unit });
    loading(false);
  };
  CreateUpdate = async () => {
    if (!this.state.product_name.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณากรอกชื่อตำแหน่ง");
      return;
    }

    loading(true);
    let body = {
      product_name: this.state.product_name, // ชื่อสินค้า
      product_type: this.state.product_type, // ประเภทสินค้า
      category_id: this.state.category_id, // หมวดหมู่สินค้าหลัก
      unit_id: this.state.unit_id, // หน่วยนับ
      profit_percent: this.state.profit_percent, // กำไร %
      minimum_price: this.state.minimum_price, // ราคาขายขั้นต่ำ (บาท/หน่วย)
      front_price: this.state.front_price, // ราคาขายหน้าร้าน (บาท/หน่วย)
      median_price: this.state.median_price, // ราคาขายกลาง (บาท/หน่วย)
      notify_expire: this.state.notify_expire, // จำนวนวันแจ้งเตือนก่อนหมดอายุ
      safety_stock: this.state.safety_stock, // จำนวนแจ้งเตือนเมื่อใกล้หมดสต็อค
      limit_stock: this.state.limit_stock, // จำนวนสูงสุดที่ควรสต็อค
    };
    let result = !this.state.product_id ? await POST("admin/manage/product/create", body) : await PUT("admin/manage/product/update/" + this.state.product_id, body);
    if (result?.status) {
      alert("#", "success");
    }
    loading(false);
  };
  Delete = async (id) => {
    if (await alert("", "warning", "ลบข้อมูล", "ยืนยันการลบรายการนี้หรือไม่ ?", "ลบข้อมูล", "ยกเลิก")) {
      loading(true);
      await DELETE("admin/manage/product/delete/" + id, null);
      console.log("55555")
      alert("#", "success");
      loading(false);
    }
  };
  Clear = () => {
    this.setState({
      product_name: "",
      product_type: "ยา",
      category_id: "",
      unit_id: "",
      profit_percent: "",
      minimum_price: "",
      front_price: "",
      median_price: "",
      notify_expire: "",
      safety_stock: "",
      limit_stock: "",
    });
  };
  render() {
    const {
      modal_create,
      modal_update,

      search,

      data,
      data_category,
      data_unit,
      count,

      product_id,
      product_name,
      product_type,
      category_id,
      unit_id,
      profit_percent,
      minimum_price,
      front_price,
      median_price,
      notify_expire,
      safety_stock,
      limit_stock,
    } = this.state;
    return (
      <div className="w-100 min-vh-100">
        <Navbar />
        <div className="p-4">
          <h3 className="mb-3">จัดการสินค้า</h3>
          <div className="d-flex flex-wrap">
            <input
              type="text"
              className="form-control wpx-250 me-2 mb-2"
              placeholder="ค้นหา..."
              onChange={(e) => {
                this.setState({ search: e.target.value });
              }}
              onKeyUp={(e) => {
                if (e.key === "Enter") {
                  this.Get(1);
                }
              }}
              value={search}
            />
            <div className="d-flex">
              <button
                className="btn btn-success px-4 me-2 mb-2"
                onClick={() => {
                  this.Get(1);
                }}
              >
                ค้นหา
              </button>
              <button
                className="btn btn-outline-success px-4 me-2 mb-2"
                onClick={() => {
                  window.location.reload();
                }}
              >
                ล้างค่า
              </button>
            </div>
            <button
              className="btn btn-success px-3 ms-auto mb-2"
              onClick={async () => {
                this.Clear();
                this.setState({ modal_create: true });
              }}
            >
              + เพิ่มข้อมูล
            </button>
          </div>
          <div className="table-responsive">
            <table className="table table-borderless rounded-3 overflow-hidden table-hover">
              <thead>
                <tr>
                  <td>สินค้า</td>
                  <td>หน่วย</td>
                  <td>ราคาขาย</td>
                  <td>หมวดหมู่</td>
                  <td>ประเภท</td>
                  <td>แจ้งเตือนก่อนหมดอายุ</td>
                  <td>แจ้งเตือนใกล้หมดสต็อค</td>
                  <td>สูงสุดที่ควรสต็อค</td>
                  <td>สถานะ</td>
                  <td className="wpx-80"></td>
                </tr>
              </thead>
              <tbody>
                {data?.map((item, index) => (
                  <tr>
                    <td>{item.product_name}</td>
                    <td>{item.unit_data?.unit_name}</td>
                    <td>{toFixed(item.front_price)} บาท</td>
                    <td>{item.category_data?.category_name}</td>
                    <td>{item.product_type}</td>
                    <td>{item.notify_expire}</td>
                    <td>{item.safety_stock}</td>
                    <td>{item.limit_stock}</td>
                    <td>{item.is_active ? Status("ใช้งาน", "success") : Status("ไมใช้งาน", "danger")}</td>
                    <td>
                      <div className="d-flex">
                        <BtnIcon
                          type="update"
                          onClick={() => {
                            this.setState({
                              ...item,
                              category_id: item.category_data?.category_id || "",
                              unit_id: item.unit_data?.unit_id,
                              modal_update: true,
                            });
                          }}
                        />
                        <BtnIcon
                          type="delete"
                          onClick={() => {
                            this.Delete(item?.product_id);
                          }}
                        />
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
          <div className="w-100 d-flex align-items-center justify-content-between flex-wrap mt-2">
            <span className="mb-3">รายการทั้งหมด {count || 0} รายการ</span>
            <Pagination className="mb-3" count={Math.ceil(count / 10) || 1} showFirstButton showLastButton onChange={(_, page) => this.Get(page)} />
          </div>
        </div>
        {/* CREATE UPDATE */}
        <Modal show={modal_create || modal_update} onHide={() => this.setState({ modal_create: false, modal_update: false })}>
          <Modal.Header closeButton>
            <h5 className="mb-0">{modal_create ? "เพิ่มข้อมูล" : modal_update ? "แก้ไขข้อมูล" : ""}</h5>
          </Modal.Header>
          <Modal.Body>
            <div className="mb-3">
              <div className="mb-2">ชื่อสินค้า</div>
              <input
                type="text"
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  this.setState({ product_name: e.target.value });
                }}
                value={product_name}
              />
            </div>
            <div className="mb-3">
              <div className="mb-2">ประเภทสินค้า</div>
              <div className="d-flex">
                <input
                  type="radio"
                  onChange={(e) => {
                    this.setState({ product_type: "ยา" });
                  }}
                  checked={product_type === "ยา"}
                />
                <span className="mx-2">ยา</span>
                <input
                  type="radio"
                  onChange={(e) => {
                    this.setState({ product_type: "บริการ" });
                  }}
                  checked={product_type === "บริการ"}
                />
                <span className="mx-2">บริการ</span>
                <input
                  type="radio"
                  onChange={(e) => {
                    this.setState({ product_type: "สินค้าหน้าร้าน" });
                  }}
                  checked={product_type === "สินค้าหน้าร้าน"}
                />
                <span className="mx-2">สินค้าหน้าร้าน</span>
              </div>
            </div>
            <div className="mb-3">
              <div className="mb-2">หมวดหมู่สินค้า</div>
              <Select
                className="form-control"
                options={data_category}
                onChange={(e) => {
                  this.setState({ category_id: e });
                }}
                value={category_id}
              ></Select>
            </div>
            <div className="mb-3">
              <div className="mb-2">หน่วยสินค้า</div>
              <Select
                className="form-control"
                options={data_unit}
                onChange={(e) => {
                  this.setState({ unit_id: e });
                }}
                value={unit_id}
              ></Select>
            </div>
            <div className="mb-3">
              <div className="mb-2">เปอร์เซ็นต์กำไรการขาย</div>
              <input
                type="text"
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  if (!float(e.target.value) || Number(e.target.value) > 100) {
                    return;
                  }
                  this.setState({ profit_percent: e.target.value });
                }}
                value={profit_percent}
              />
            </div>
            <div className="mb-3">
              <div className="mb-2">ราคาขายขั้นต่ำ (บาท/หน่วย)</div>
              <input
                type="text"
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  if (!float(e.target.value)) {
                    return;
                  }
                  this.setState({ minimum_price: e.target.value });
                }}
                value={minimum_price}
              />
            </div>
            <div className="mb-3">
              <div className="mb-2">ราคาขายหน้าร้าน (บาท/หน่วย)</div>
              <input
                type="text"
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  if (!float(e.target.value)) {
                    return;
                  }
                  this.setState({ front_price: e.target.value });
                }}
                value={front_price}
              />
            </div>
            <div className="mb-3">
              <div className="mb-2">ราคากลาง (บาท/หน่วย)</div>
              <input
                type="text"
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  if (!float(e.target.value)) {
                    return;
                  }
                  this.setState({ median_price: e.target.value });
                }}
                value={median_price}
              />
            </div>
            <div className="mb-3">
              <div className="mb-2">จำนวนวันแจ้งเตือนก่อนหมดอายุ</div>
              <input
                type="text"
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  if (!float(e.target.value)) {
                    return;
                  }
                  this.setState({ notify_expire: e.target.value });
                }}
                value={notify_expire}
              />
            </div>
            <div className="mb-3">
              <div className="mb-2">จำนวนแจ้งเตือนเมื่อใกล้หมดสต็อค</div>
              <input
                type="text"
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  if (!float(e.target.value)) {
                    return;
                  }
                  this.setState({ safety_stock: e.target.value });
                }}
                value={safety_stock}
              />
            </div>
            <div className="mb-3">
              <div className="mb-2">จำนวนสูงสุดที่ควรสต็อค</div>
              <input
                type="text"
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  if (!float(e.target.value)) {
                    return;
                  }
                  this.setState({ limit_stock: e.target.value });
                }}
                value={limit_stock}
              />
            </div>
          </Modal.Body>
          <Modal.Footer className="py-1 d-flex justify-content-end">
            <button
              className="btn btn-success px-5 me-1"
              onClick={() => {
                this.CreateUpdate();
              }}
            >
              บันทึก
            </button>
            <button
              className="btn btn-secondary px-5 ms-1"
              onClick={() => {
                this.setState({ modal_create: false, modal_update: false });
              }}
            >
              ยกเลิก
            </button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}
