import React, { Component } from "react";
import { profile, branch, alert, total, float, toFixed, format_date, random_charactor, GET, POST, PUT, DELETE, loading, Status, params, DatePicker, Select, BtnIcon, Swal, Pagination, Switch, Navbar, Modal, Resizer }from "../../../components/CustomComponent.js";
export default class Category extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modal_create: false,
      modal_update: false,

      search: "",

      data: [],
      count: 0,

      category_name: "",
      is_active: false,
    };
  }

  async componentDidMount() {
    loading(false);
    await this.Get(1);
  }
  Get = async (page) => {
    loading(true);
    let result = await GET("admin/manage/category/list", { page: page, data_search: this.state.search });
    if (result?.status) {
      this.setState({ data: result.data, count: result.count });
    }
    loading(false);
  };
  CreateUpdate = async () => {
    if (!this.state.category_name.trim()) {
      alert("", "warning", "แจ้งเตือน", "กรุณากรอกหมวดหมู่สินค้า");
      return;
    } 

    loading(true);
    let body = {
      category_name: this.state.category_name,
      is_active: this.state.is_active,
    };
    let result = !this.state.category_id ? await POST("admin/manage/category/create", body) : await PUT("admin/manage/category/update/" + this.state.category_id, body);
    if (result?.status) {
      alert("#", "success");
    }
    loading(false);
  };
  Delete = async (id) => {
    if (await alert("", "warning", "ลบข้อมูล", "ยืนยันการลบรายการนี้หรือไม่ ?", "ลบข้อมูล", "ยกเลิก")) {
      loading(true);
      let result = await DELETE("admin/manage/category/delete/" + id, null);
      if (result?.status) {
        alert("#", "success");
      }
      loading(false);
    }
  };
  render() {
    const {modal_create,
      modal_update,
      search,
      data,
      count,
      category_name,
      is_active} = this.state;
    return (
      <div className="w-100 min-vh-100">
        <Navbar />
        <div className="p-4">
          <h3 className="mb-3">หมวดหมู่สินค้า</h3>
          <div className="d-flex flex-wrap">
            <input
              type="text"
              className="form-control wpx-250 me-2 mb-2"
              placeholder="ค้นหา..."
              onChange={(e) => {
                this.setState({ search: e.target.value });
              }}
              onKeyUp={(e) => {
                if (e.key === "Enter") {
                  this.Get(1);
                }
              }}
              value={search}
            />
            <div className="d-flex">
              <button
                className="btn btn-success px-4 me-2 mb-2"
                onClick={() => {
                  this.Get(1);
                }}
              >
                ค้นหา
              </button>
              <button
                className="btn btn-outline-success px-4 me-2 mb-2"
                onClick={() => {
                  window.location.reload();
                }}
              >
                ล้างค่า
              </button>
            </div>
            <button
              className="btn btn-success px-3 ms-auto mb-2"
              onClick={async () => {
                this.setState({ modal_create: true,category_id: "",category_name: "",is_active: true });
              }}
            >
              + เพิ่มข้อมูล
            </button>
          </div>
          <div className="table-responsive">
            <table className="table table-borderless rounded-3 overflow-hidden table-hover">
              <thead>
                <tr>
                  <td>หมวดหมู่สินค้า</td>
                  <td>สถานะ</td>
                  <td className="wpx-80"></td>
                </tr>
              </thead>
              <tbody>
                {data?.map((item, index) => (
                  <tr>
                    <td>{item.category_name}</td>
                    <td>{item.is_active ? Status("ใช้งาน","success") : Status("ไมใช้งาน","danger")}</td>
                    <td>
                      <div className="d-flex">
                        <BtnIcon
                          type="update"
                          onClick={() => {
                            this.setState({
                              ...item,
                              modal_update: true,
                            });
                          }}
                        />
                        <BtnIcon
                          type="delete"
                          onClick={() => {
                            this.Delete(item?.category_id);
                          }}
                        />
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
          <div className="w-100 d-flex align-items-center justify-content-between flex-wrap mt-2">
            <span className="mb-3">รายการทั้งหมด {count || 0} รายการ</span>
            <Pagination className="mb-3" count={Math.ceil(count / 10) || 1} showFirstButton showLastButton onChange={(_, page) => this.Get(page)} />
          </div>
        </div>
        {/* CREATE UPDATE */}
        <Modal show={modal_create || modal_update} onHide={() => this.setState({ modal_create: false, modal_update: false })}>
          <Modal.Header closeButton>
            <h5 className="mb-0">{modal_create ? "เพิ่มหมวดหมู่สินค้า" : modal_update ? "แก้ไขหมวดหมู่สินค้า" : ""}</h5>
          </Modal.Header>
          <Modal.Body>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">หมวดหมู่สินค้า</div>
              <input
                type="text"
                className="form-control"
                placeholder="กรอกข้อมูล..."
                onChange={(e) => {
                  this.setState({ category_name: e.target.value });
                }}
                value={category_name}
              />
            </div>
            <div className="d-flex mb-2">
              <div className="wpx-120 mt-2">สถานะ</div>
              <div className="mb-3">
                               <div className="d-flex align-items-center">
                                 <Switch
                                   className="me-3"
                                   onChange={(e) => {
                                     this.setState({ is_active: e });
                                   }}
                                   checked={is_active}
                                 />
                                 <span>{!is_active && "ไม่"}ใช้งาน</span>
                               </div>
                             </div>
            </div>
          </Modal.Body>
          <Modal.Footer className="py-1 d-flex justify-content-end">
            <button
              className="btn btn-success px-5 me-1"
              onClick={() => {
                this.CreateUpdate();
              }}
            >
              บันทึก
            </button>
            <button
              className="btn btn-secondary px-5 ms-1"
              onClick={() => {
                this.setState({ modal_create: false, modal_update: false });
              }}
            >
              ยกเลิก
            </button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}
